import React, { useEffect, Fragment, useMemo, useState } from 'react';
import {
  Row,
  Button,
  Card,
  CardBody,
  CardTitle,
} from 'reactstrap';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import Pagination from '../../../components/pages/Pagination';
import OrderServices from '../../../service/graphql/Order';
import {
  BaseTable,
  BaseModal,
 } from '../common';
import {
  ORDER_TABLE_ITEMS,
  PAGE_SIZE,
} from './constants';
import OrderView from './OrderView';
import { getDeliveryTimeFromHubTiming } from '../../../utils/utils';

const Orders = props => {

  const { match, history } = props;

  const pageSize = PAGE_SIZE;
  const [orders, setOrders] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPage, setTotalPage] = useState(1);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [orderDetail, setOrderDetail] = useState(null);
  const [showOrderViewModal, setShowOrderViewModal] = useState(false);

  const openDeleteModal = () => setShowDeleteModal(true);
  const closeDeleteModal = () => setShowDeleteModal(false);
  const openOrderViewModal = () => setShowOrderViewModal(true);
  const closeOrderViewModal = () => setShowOrderViewModal(false);

  const getOrders = () => {
    const query = {
      page: currentPage,
      pageSize: pageSize,
      keyword: '',
    };

    OrderServices.saleGetOrders(query, res => {
      if (res.status === 1) {
        const numPages = Math.ceil((res?.data?.total || 0) / pageSize);
        const _orders = res?.data?.results || [];
        
        setOrders(_orders.map(_order => {
          return {
            ..._order,
            timeslotlabel: getDeliveryTimeFromHubTiming(
              _order?.timeslot_data?.delivery_start_time,
              _order?.timeslot_data?.delivery_end_time
            )
          };
        }));
        setTotalPage(numPages|| 1);
      }
    });
  };

  const handleDeleteOrder = item => {
    setSelectedOrder(item);
    openDeleteModal();
  };

  const deleteOrder = () => {
    const query = {
      id: selectedOrder.order_id
    };

    OrderServices.saleDeleteOrder(query, res => {
      if (res.status === 1) {
        getOrders();
        closeDeleteModal();
      }
    });

  };

  const onChangePage = i => {
    setCurrentPage(i);
  };

  useEffect(() => {
    getOrders();
  }, [pageSize, currentPage]);

  const orderTableItems = useMemo(() => {
    const _orderTableItems = ORDER_TABLE_ITEMS;

    return _orderTableItems;
  }, []);

  const handleViewOrderModal = orderId => {
    const query = {
      id: orderId,
    };

    OrderServices.saleGetOrderStatusWithProof(query, res => {
      if (res.status === 1) {
        const _orderDetail = res?.data;
        setOrderDetail(_orderDetail);
        openOrderViewModal();
      }
    });
  };

  return (
    <Fragment>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="Orders" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Colxx xxs="12" className="orders">
        <Card>
          <CardBody>
            <CardTitle>List of Orders</CardTitle>
            <BaseTable
              data={orders}
              columns={orderTableItems}
              actions={(item, index) => {
                return (
                  <div key={'order-buttons' + item?.id + index}>
                    <i
                      className="simple-icon-pencil"
                      style={{ cursor:'pointer' }}
                      onClick={() => history.push(`/app/orders/order-detail/${item._id}`)}
                    />
                    <i
                      className="simple-icon-eye"
                      style={{ marginLeft: '10px', cursor:'pointer' }}
                      onClick={() => handleViewOrderModal(item._id)}
                    />
                    <i
                      className="simple-icon-trash"
                      style = {{ marginLeft: '10px', cursor:'pointer' }}
                      onClick={() => handleDeleteOrder(item)}
                    />
                  </div>
                );
              }}
            />
          </CardBody>
        </Card>
      </Colxx>
      <Pagination
        currentPage={currentPage}
        totalPage={totalPage}
        onChangePage={(i) => onChangePage(i)}
      />
      <BaseModal
        open={showDeleteModal}
        handleOpen={open => setShowDeleteModal(open)}
        title="Warning"
        content="Are you sure to delete this order?"
        buttons={
          <>
            <Button color="primary" onClick={deleteOrder}>Yes</Button>
            <Button color="secondary" onClick={closeDeleteModal}>Cancel</Button>
          </>
        }
      />
      <OrderView
        open={showOrderViewModal}
        handleOpen={open => setShowOrderViewModal(open)}
        data={orderDetail}
      />
    </Fragment>
  );
};

export default Orders;
