import React,{useEffect,useState} from "react";
import {
  CustomInput,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Row, 
  Form
  
} from "reactstrap";
import "react-datepicker/dist/react-datepicker.css";
import IntlMessages from "../../helpers/IntlMessages";
import LanguageSelector from '../LanguageSelector'
import {plainListToGql,gqlToLanguageOptions,getDefaultLanguageOptions} from '../../libs/converter'
import StateService from '../../service/graphql/State'
import AsyncSelect from 'react-select/async'
import CustomSelectInput from "../../components/common/CustomSelectInput";

const AddNewModal = ({ modalOpen,selectedItem, toggleModal,onSubmit,closeModal,onChangeProperty }) => {
  
  const [langOptions,setLangOptions] = useState([])
  const [stateOptions,setStateOptions] = useState([])
  const [selectedLangIndex,setSelectedLangIndex] = useState(0)
  const [isSubmitting, setIsSubmitting] = useState(false)
  useEffect(()=>{
      getDefaultLanguageOptions((options)=>{
        
        if(selectedItem.langs){
          const tempLangOptions = gqlToLanguageOptions(selectedItem.langs,"city_label")
          setLangOptions(tempLangOptions)
          selectedItem.lang_data = plainListToGql(tempLangOptions)

        }else{
          setLangOptions(options)
          selectedItem.lang_data = plainListToGql(options)
          
        }
      })
      getStates()
  },[ modalOpen])
  const getStates = ()=>{
    StateService.getStates(1,10,"",response=>{
        if(response.status === 1){
          const operators = response.data.results
          const tempOptions = []
          operators.forEach(element => {
              const obj = {
                label:element.state_name,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          setStateOptions(tempOptions)
        }
    })
  }
  const onChangeName = (value)=>{
    const selectedLangOption = langOptions[selectedLangIndex];
    selectedLangOption.title = value
    setLangOptions([...langOptions])
    selectedItem.lang_data = plainListToGql(langOptions)
    

  }
  const loadStateOptions=(inputValue, callback)=>{
    StateService.getStates(1, 15, inputValue, response=>{
      if(response.status === 1){
          const operators = response.data.results
          const tempOptions = []
          operators.forEach(element => {
              const obj = {
                label:element.state_name,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          callback(tempOptions)
        }
    })
  };
  const getSelectedOption = (value)=>{
    const found = stateOptions.find((item)=>{
       return(
         item.value === value
       )
     })
     return found;
 }
 const handleSubmit = (e)=>{
  
  e.stopPropagation();
  e.preventDefault();
  setIsSubmitting(true)
  if(!selectedItem.state_id){
    return
  }
  onSubmit();
}
  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
          {selectedItem._id?"Edit City":"Add New City"}
      </ModalHeader>
      <Form onSubmit={handleSubmit}>
      <ModalBody>
      <Label className="mt-4">
            City Name
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'city_name')}} value={selectedItem.city_name}/>
        <Label className="mt-4">
            State
        </Label>
        <AsyncSelect
          components={{ Input: CustomSelectInput }}
          className="react-select"
          name="form-field-name"
          loadOptions={loadStateOptions}
          defaultOptions
          value={getSelectedOption(selectedItem.state_id)}
          onChange={(e)=>{onChangeProperty(e.value,'state_id')}}
        />
        {
          isSubmitting && !selectedItem.state_id  &&
          <div className="invalid-feedback d-block">
                {"State required!"}
          </div>
        }
        <LanguageSelector 
            langOptions={langOptions} 
            onChangeName={(e)=>{onChangeName(e)}}
            selectedLangIndex = {selectedLangIndex}
            onChangeTab = {(index)=>{setSelectedLangIndex(index)}}
        />
      </ModalBody>
      
      <ModalFooter>
        <Button color="secondary" outline onClick={closeModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button color="primary" type="submit">
          <IntlMessages id="pages.submit" />
        </Button>{" "}
      </ModalFooter>
      </Form>
    </Modal>
  );
};

export default AddNewModal;
