import gql from 'graphql-tag';
import GRAPHQL from './index'

export default class {
  
  static getApiLogs(data, callback) {
    const query = {
      query: gql`
        {
          getApiLogs(
            page: ${data.page}, 
            pageSize:${data.pageSize},
            keyword:"${data.keyword}",
            result:"${data.result}",
            ) {
            status
            message
            success
            data
          }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.getApiLogs.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

  static getApiLogDetail(data, callback) {
    const query = {
      query: gql`
        {
          getApiLogDetail(
            log_id: "${data.id}"
          ) {
            status
            message
            success
            data
          }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.getApiLogDetail.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

  static deleteApiLog(data, callback) {
    const query = {
      mutation: gql`
        mutation {
          deleteApiLog(log_id: "${data.id}") {
            status
            message
            success
            data
          }
        }
      `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.deleteApiLog.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      });
  }

}
