import React,{useEffect,useState} from "react";
import {
  CustomInput,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Row, 
  
} from "reactstrap";
import "react-datepicker/dist/react-datepicker.css";
import IntlMessages from "../../helpers/IntlMessages";
import LanguageSelector from '../LanguageSelector'
import CustomSelectInput from "../../components/common/CustomSelectInput";
import AsyncSelect from 'react-select/async'
import {plainListToGql,gqlToLanguageOptions,getDefaultLanguageOptions} from '../../libs/converter'
import ProductOptionService from '../../service/graphql/ProductOption'

const AddNewModal = ({ modalOpen,selectedItem, toggleModal,onSubmit,closeModal,onChangeProperty }) => {
  
  const [langOptions,setLangOptions] = useState([])
  const [selectedLangIndex,setSelectedLangIndex] = useState(0)
  const [productOptions, setProductOptions] = useState([])
  useEffect(()=>{
      getProductOptions()
      getDefaultLanguageOptions((options)=>{
        if(selectedItem.langs){
          const tempLangOptions = gqlToLanguageOptions(selectedItem.langs,"optionvalue_label")
          setLangOptions(tempLangOptions)
          selectedItem.lang_data = plainListToGql(tempLangOptions)

        }else{
          setLangOptions(options)
        }
      })
      
  },[selectedItem.langs])
  const getSelectedOption = (value)=>{
    const found = productOptions.find((item)=>{
       return(
         item.value === value
       )
     })
     return found;
 }
  const  getProductOptions = ()=>{
    ProductOptionService.getProductOptions(response=>{
        if(response.status === 1){
          const operators = response.data.operators
          const tempOptions = []
          operators.forEach(element => {
              const obj = {
                label:element.operator_partner_identifier,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          setProductOptions(tempOptions)
        }
    })
  }
  const loadOptions = (inputValue, callback) => {
    ProductOptionService.getProductOptions(response=>{
      if(response.status === 1){
          const operators = response.data.results
          const tempOptions = []
          operators.forEach(element => {
              const obj = {
                label:element.option_identifier,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          callback(tempOptions)
        }
    })
  };


  const onChangeName = (value)=>{
    const selectedLangOption = langOptions[selectedLangIndex];
    selectedLangOption.title = value
    setLangOptions([...langOptions])
    selectedItem.lang_data = plainListToGql(langOptions)
    
    
  }
  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
          {selectedItem._id?"Edit Product Option Value":"Add New Product Option Value"}
      </ModalHeader>
      <ModalBody>
        <Label className="mt-4">
            Identifier
        </Label>
        <Input onChange={(e)=>{onChangeProperty(e.target.value,'optionvalue_identifier')}} value={selectedItem.optionvalue_identifier}/>
        {/* <Label className="mt-4">
            Product Option
        </Label> */}
        {/* <AsyncSelect
          components={{ Input: CustomSelectInput }}
          className="react-select"
          name="form-field-name"
          loadOptions={loadOptions}
          defaultOptions
          value={getSelectedOption(selectedItem.option_id)}
          onChange={(e)=>{onChangeProperty(e.value,'option_id')}}
          // onInputChange={handleInputChange}
        /> */}
        <LanguageSelector 
            langOptions={langOptions} 
            onChangeName={(e)=>{onChangeName(e)}}
            selectedLangIndex = {selectedLangIndex}
            onChangeTab = {(index)=>{setSelectedLangIndex(index)}}
        />
      </ModalBody>
      
      <ModalFooter>
        <Button color="secondary" outline onClick={closeModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button color="primary" onClick={onSubmit}>
          <IntlMessages id="pages.submit" />
        </Button>{" "}
      </ModalFooter>
    </Modal>
  );
};

export default AddNewModal;
