import React, { Component, Fragment } from "react";
import { Row,Button,Modal , ModalHeader,
  ModalBody,
  Card, CardBody, CardTitle,
  ModalFooter, Table} from "reactstrap";
  
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import AddNewOperatorModal from "../../../components/modals/AddNewOperatorModal";
import OperatorService from '../../../service/graphql/Operator'
import moment from 'moment'
import { NotificationManager } from "../../../components/common/react-notifications";
// import ListPageHeading from '../../components/pages/ListPageHeading'

export default class Operators extends Component {
    constructor(props){
      super(props)
      this.state={
        modal:true,
        showDeleteModalFlag:false,
        selectedItem:{},
        selectedOperator:{},
        operators:[],
        currentPage:1,
        pageSize: 10,
        totalPage: 0
      }
    }
    componentDidMount(){
        this.refresh()
    }
    refresh(){
      OperatorService.getOperators({page: this.state.currentPage, pageSize: this.state.pageSize}, response=>{
          if(response.status === 1){
            console.log("response.data----------->>>>",response.data)
            this.setState({
              operators:response.data.operators,
              currentPage:response.data.page,
              pageSize: response.data.pageSize,
              totalPage: Math.ceil(response.data.total / response.data.pageSize)
            })
            // console.log("response.data===>",response.data)
          }
      })
    }
    onChangePage(i){
      this.setState({currentPage:i}, this.refresh)
    }
    closeModal = () => {
      this.setState({
        modalOpen: false
      });
    };
    showModal = () => {
      this.setState({
        modalOpen: true
      });
    };
    toggle =()=>{
      this.setState({
        showDeleteModalFlag: false
      });
    }
    showDeleteModal(item,e){
      e.stopPropagation();
      e.preventDefault();
      this.setState({showDeleteModalFlag:true, selectedItem:item})
    }
    deleteOperator(){
      const {selectedItem} = this.state
      console.log("selectedItem ..... on ----L>", selectedItem)
      OperatorService.deleteOperator(selectedItem._id,(error,res)=>{
         this.refresh()
      })
    }
    onNewInstance(e){
        const {selectedItem} = this.state
        if((selectedItem.operator_partner_identifier === undefined || selectedItem.operator_partner_identifier.length === 0) ||
           (selectedItem.api_endpoint === undefined || selectedItem.api_endpoint.length === 0)){
          NotificationManager.error(
            "Error message",
            "Please confirm valid data!",
            3000,
            () => {
              // alert("callback");
            },
            null,
            "error"
          );
          return;
        }
        OperatorService.postOperator(this.state.selectedItem,(error,res)=>{
              this.closeModal()
              this.refresh()
        })
    }
    onChangeProperty(value,type){
      const {selectedItem} = this.state
      if(type==="image"){
        let reader = new FileReader();
        reader.readAsDataURL(value);
        reader.onloadend = () => {
          selectedItem[type] = value
          this.setState({selectedItem})          
        };
      }else{
        selectedItem[type] = value
        this.setState({selectedItem})
      }
      
    }
    render() {
        const {modalOpen,selectedOperator,operators,selectedItem} = this.state
        return (
            <Fragment>
            <Row >
              <Colxx xxs="12">
                <Breadcrumb heading="Operators" match={this.props.match} />
                <Separator className="mb-5" />
              </Colxx>
            </Row>
            <Row className="display-flex flex-row-reverse mb-4 mr-4">
              <Button
                color="primary"
                size="lg"
                className="top-right-button"
                onClick={()=>{this.showModal();this.setState({selectedItem:{}})}}>
                Add New
              </Button>
            </Row>
            <Colxx xxs="12">
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    List of Operators
                  </CardTitle>
                  <Table hover>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Partner Idnetifier</th>
                        <th>API endpoint</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        operators && operators.map((item,index)=>{
                          return(
                            <tr
                            key = {"operator_" + index}
                            style = {{cursor:'pointer'}}
                            onClick={()=>{this.setState({modalOpen:true, selectedItem:item})}}
                            >
                              <th scope="row">{item._id}</th>
                              <td>{item.operator_partner_identifier}</td>
                              <td>{item.api_endpoint}</td>
                              <td >
                                <div className="action-group">
                                  <i className="simple-icon-pencil" onClick={()=>{this.setState({modalOpen:true, selectedItem:item})}}/>
                                  <i style = {{marginLeft:'10px'}} className="simple-icon-trash" onClick={this.showDeleteModal.bind(this,item)}/> 
                                </div>
                              </td>
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Colxx>
             <Modal isOpen={this.state.showDeleteModalFlag} toggle={this.toggle}>
                    <ModalHeader toggle={this.toggle}>
                      Warning
                      {/* <IntlMessages id="modal.modal-title" /> */}
                    </ModalHeader>
                    <ModalBody>
                          Are you sure to delete this operator?
                    </ModalBody>
                    <ModalFooter>
                      <Button color="primary" onClick={()=>{this.toggle();this.deleteOperator()}}>
                        Yes
                      </Button>{" "}
                      <Button color="secondary" onClick={this.toggle}>
                        Cancel
                      </Button>
                    </ModalFooter>
                  </Modal>
            <AddNewOperatorModal
              selectedItem = {selectedItem}
              modalOpen = {modalOpen}
              closeModal={this.closeModal}
              onChangeProperty={this.onChangeProperty.bind(this)}
              selectedOperator = {selectedOperator}
              onSubmit={this.onNewInstance.bind(this)}
              // categories={categories}
            />
            <Pagination
              currentPage={this.state.currentPage}
              totalPage={this.state.totalPage}
              onChangePage={i => this.onChangePage(i)}
            />
          </Fragment>
        )
    }
}
