import React, { Component, Fragment } from "react";
import { Row,Button,Modal , ModalHeader,
  ModalBody,Card, CardBody, CardTitle,Table,
  ModalFooter, } from "reactstrap";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import ShopService from '../../../service/graphql/Shop'


export default class Shops extends Component {
    constructor(props){
      super(props)
      this.state={
        modal:true,
        showDeleteModal:false,
        items:[],
        shops:[],
        selectedPromotion:{},
        currentPage:1,
        pageSize:10
      }
    }
    componentDidMount(){
      this.refresh()
     }
     refresh(){
      ShopService.getShops(this.state.currentPage, this.state.pageSize,response=>{
          if(response.status === 1){
            console.log("response.data.results----------->>>>",response.data.results)
            this.setState({
              shops:response.data.results,
              currentPage:response.data.page,
              totalPage: Math.ceil(response.data.total / response.data.pageSize)
            })
          }
      })
    }
    onChangePage(i){
      this.setState({currentPage:i})
    }
    toggleModal = () => {
      this.setState({
        modalOpen: !this.state.modalOpen
      });
    };
    toggle =()=>{
      this.setState({
        showDeleteModal: !this.state.showDeleteModal
      });
    }
    showDeleteModal(item,e){
      e.stopPropagation();
      e.preventDefault();

      this.setState({showDeleteModal:true,selectedItem:item})

    }
    deleteShopApply(){
      const {selectedItem} = this.state
      ShopService.deleteShop(selectedItem._id,(error,res)=>{
         this.refresh()
     })
    }
    handleChangeStart=(e)=>{
      const {selectedPromotion} = this.state
      selectedPromotion.startDateRange = e
      this.setState({selectedPromotion})
    }
    handleChangeEnd=(e)=>{
      const {selectedPromotion} = this.state
      selectedPromotion.endDateRange = e
      this.setState({selectedPromotion})
    }
    showDaysDetails(item,e){
      e.stopPropagation();
      e.preventDefault();
      this.props.history.push({
        pathname: `/app/shops/shop-days-detail/${item._id}`,
        state: { hubId: item.hub_id?item.hub_id:'' }
      })
    }
    render() {
        return (
            <Fragment>
            <Row >
              <Colxx xxs="12">
                <Breadcrumb heading="Shops" match={this.props.match} />
                <Separator className="mb-5" />
              </Colxx>
            </Row>
            <Row className="display-flex flex-row-reverse mb-4 mr-4">
            </Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                     List of Shops
                  </CardTitle>
                  <Table hover>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Identifier</th>
                        <th>User</th>
                        <th>Hub</th>
                        <th>Country Code</th>
                        <th>Phone</th>
                        <th>Address</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        this.state.shops.map((item,index)=>{
                          return(
                            <tr
                            className="cursor-pointer"
                            onClick={()=>{this.props.history.push(`/app/shops/shop-detail/${item._id}`);}}
                            >
                              <th scope="row">{item._id}</th>
                              <td>{item.shop_identifier}</td>
                              <td>{item.shop_user_name}</td>
                              <td>{item.hub_name}</td>
                              <td>{item.country_code}</td>
                              <td>{item.shop_phone_number}</td>
                              <td>{item.shop_address}</td>
                              <td>
                                <div className="action-group">
                                  <i className="simple-icon-calendar" style={{marginRight:'5px'}} onClick={this.showDaysDetails.bind(this,item)}/> 
                                  <i className="simple-icon-pencil ml-3 mr-3" onClick={()=>{this.props.history.push(`/app/shops/shop-detail/${item._id}`);}}/> 
                                  <i className="simple-icon-trash" onClick={this.showDeleteModal.bind(this,item)}/> 
                                </div>
                              </td>
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Colxx>
             <Modal isOpen={this.state.showDeleteModal} toggle={this.toggle}>
                    <ModalHeader toggle={this.toggle}>
                      Warning
                    </ModalHeader>
                    <ModalBody>
                          Are you sure to delete this shop?
                    </ModalBody>
                    <ModalFooter>
                      <Button color="primary" onClick={()=>{this.toggle();this.deleteShopApply()}}>
                        Yes
                      </Button>{" "}
                      <Button color="secondary" onClick={this.toggle}>
                        Cancel
                      </Button>
                    </ModalFooter>
                  </Modal>
            <Pagination
              currentPage={this.state.currentPage}
              totalPage={this.state.totalPage}
              onChangePage={i => this.onChangePage(i)}
            />
          </Fragment>
        )
    }
}
