import React, { Component, Fragment } from "react";
import { Row, Button, Card, CardBody, Table } from "reactstrap";
import moment from "moment";

import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import APIServices from "../../../service/api/api";

import { DATA_FORMAT_WITHOUT_TIME } from "../../../constants/defaultValues";
import { Link } from "react-router-dom";

export default class Prefunds extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPage: 1,
      totalCategories: {},
      prefunds: [],
      totalPage: 0,
      pageSize: 10,
    };
  }
  componentDidMount() {
    this.refresh(this.state.currentPage);
  }
  refresh(currentPage) {
    const page = currentPage;
    const size = this.state.pageSize;
    APIServices.getPrefunds(page, size, (err, res) => {
      console.log("res: ", res);
      if (res && res.success) {
        const prefunds = res.data.data;
        this.setState({
          prefunds,
          totalPage: Math.ceil(res.data.total / res.data.size),
        });
      } else {
      }
    });
  }

  onChangePage(i) {
    this.setState({ currentPage: i });
    setImmediate(() => {
      const { currentPage } = this.state;
      this.refresh(currentPage);
    });
  }
  render() {
    const { prefunds } = this.state;
    console.log("this.props: ", this.props);
    return (
      <Fragment>
        <Row>
          <Colxx xxs="12">
            <Breadcrumb heading="Prefunds" match={this.props.match} />
            <Separator className="mb-5" />
          </Colxx>
        </Row>
        <Colxx>
          <Card className="mb-4">
            <CardBody>
              {/* <CardTitle>
                    List of prefunds
                  </CardTitle> */}
              <Table>
                <thead>
                  <tr>
                    <th>Id</th>
                    <th>From</th>
                    <th>To</th>
                    <th>Amount</th>
                    <th>Created At</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {prefunds &&
                    prefunds.length > 0 &&
                    prefunds.map((item, index) => {
                      return (
                        <tr key={index}>
                          <th style={{ verticalAlign: "middle" }} scope="row">
                            {item.id}
                          </th>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.from}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>{item.to}</td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.amount}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.createdAt
                              ? moment(item.createdAt).format(
                                  DATA_FORMAT_WITHOUT_TIME
                                )
                              : "-"}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            <div className="action-group">
                              <a
                                href={`http://tencent.blockchain.testnet.sharetoken.io:1317/bank/balances/${item.to}`}
                                target="_blank"
                                rel="noopener noreferrer"
                              >
                                <i className="simple-icon-eye" />
                              </a>
                            </div>
                          </td>
                        </tr>
                      );
                    })}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Colxx>
        <Pagination
          currentPage={this.state.currentPage}
          totalPage={this.state.totalPage}
          onChangePage={(i) => this.onChangePage(i)}
        />
      </Fragment>
    );
  }
}
