import React from 'react';
import { Table, Input } from 'reactstrap';
import { get } from 'lodash';
import { TABLE_ITEM_TYPES } from '../../../constants/defaultValues';

const EditableTable = props => {

  const {
    data,
    columns,
    actions,
    onChange,
    ...restProps
  } = props;

  const handleChange = (e, id, key, type) => {
    if (type === TABLE_ITEM_TYPES.number) {
      if (e.target.value >= e.target.min) {
        onChange(e, id, key);
      }
    } else {
      onChange(e, id, key);
    }
  };

  return (
    <Table
      className={data && data.length ? 'table-responsive d-table' : 'd-table'}
      {...restProps}
    >
      <thead>
        <tr>
          {columns.map(item => {
            return (
              <th
                key={item.name}
                className={item.className || ''}
                style={item.style || {}}
              >
                <span>{item.name}</span>
              </th>
            );
          })}
          {actions && <th>Action</th>}
        </tr>
      </thead>
      <tbody>
        {data &&
          data.length > 0 &&
          data.map((item, index) => {
            return (
              <>
                <tr key={index}>
                  {columns.map(tableItem => {
                    const itemValue = get(item, tableItem.key) || '';

                    return (
                      <td
                        key={tableItem.key}
                        className={tableItem.className || ''}
                        style={tableItem.style || {}}
                      >
                        {tableItem.editable ? (
                          <Input
                            required
                            onChange={e => handleChange(e, index, tableItem.key, tableItem?.type)}
                            value={itemValue}
                            type={TABLE_ITEM_TYPES.number === tableItem?.type ? 'number' : 'text'}
                            style={{
                              padding: '0.25rem'
                            }}
                            min={tableItem.min}
                          />
                        ) : (
                          <span>
                            {(itemValue || '-')}
                          </span>
                        )}
                      </td>
                    )
                  })}
                  {actions && <td>{actions(item, index)}</td>}
                </tr>
              </>
            );
          })}
      </tbody>
    </Table>
      
  )
};

export default EditableTable;

