import React, { Component, Fragment } from "react";
import { Row,Button,Modal , ModalHeader,
  ModalBody,
  Card, CardBody, CardTitle,
  ModalFooter, Table} from "reactstrap";
  
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import AddNewProductOptionValue from "../../../components/modals/AddNewProductOptionValue";
import ProductOptionValueService from '../../../service/graphql/ProductOptionValue'

import { NotificationManager } from "../../../components/common/react-notifications";
// import ListPageHeading from '../../components/pages/ListPageHeading'

export default class ProductOptionValues extends Component {
    constructor(props){
      super(props)
      this.state={
        modal:true,
        showDeleteModalFlag:false,
        selectedItem:{},
        selectedProductOptionValue:{},
        productoptionvalues:[],
        currentPage:1,
        selectedOptionId:null
      }
    }
    componentDidMount(){
        this.refresh()
    }
    refresh(){
      if(this.props.match.params.optionId &&  this.props.match.params.optionId!=="0"){
          let optionId = this.props.match.params.optionId
          this.setState({selectedOptionId:optionId})
          ProductOptionValueService.getProductOptionValues(optionId,response=>{
              if(response.status === 1){
                console.log("response.data----------->>>>",response.data)
                this.setState({
                  productoptionvalues:response.data.results,
                  currentPage:response.data.page,
                })
                // console.log("response.data===>",response.data)
              }
          })
      }
      
    }
    onChangePage(i){
      this.setState({currentPage:i})
    }
    closeModal = () => {
      this.setState({
        modalOpen: false
      });
    };
    showModal = () => {
      this.setState({
        modalOpen: true
      });
    };
    toggle =()=>{
      this.setState({
        showDeleteModalFlag: false
      });
    }
    showDeleteModal(item,e){
      e.stopPropagation();
      e.preventDefault();
      this.setState({showDeleteModalFlag:true, selectedItem:item})
    }
    deleteProductOptionValue(){
      const {selectedItem} = this.state
      console.log("selectedItem ..... on ----L>", selectedItem)
      ProductOptionValueService.deleteProductOptionValue(selectedItem._id,(error,res)=>{
         this.refresh()
      })
    }
    onNewInstance(e){
        const {selectedItem} = this.state
        console.log("selected item on new instance..... ", selectedItem)
        if(selectedItem.optionvalue_identifier === undefined || selectedItem.optionvalue_identifier.length === 0){
          NotificationManager.error(
            "Error message",
            "Please confirm valid data!",
            3000,
            () => {
              // alert("callback");
            },
            null,
            "error"
          );
          return;
        }
        selectedItem.option_id = this.state.selectedOptionId
        console.log("selected item on new instance..... ", selectedItem)
        ProductOptionValueService.postProductOptionValue(this.state.selectedItem,(error,res)=>{
              this.closeModal()
              this.refresh()
        })
    }
    onChangeProperty(value,type){
      const {selectedItem} = this.state
      if(type==="image"){
        let reader = new FileReader();
        reader.readAsDataURL(value);
        reader.onloadend = () => {
          selectedItem[type] = value
          this.setState({selectedItem})          
        };
      }else{
        selectedItem[type] = value
        this.setState({selectedItem})
      }
      
    }
    render() {
        const {modalOpen,selectedProductOptionValue,productoptionvalues,selectedItem} = this.state
        return (
            <Fragment>
            <Row >
              <Colxx xxs="12">
                <Breadcrumb heading="Product Option Values" match={this.props.match} />
                <Separator className="mb-5" />
              </Colxx>
            </Row>
            <Row className="display-flex mb-4 mr-4 justify-content-between align-items-center">
              <div className="back-button"
                onClick={()=>{this.props.history.goBack()}}
              >{"<-  Back to Option"}</div>
              <Button
                color="primary"
                size="lg"
                className="top-right-button"
                onClick={()=>{this.showModal();this.setState({selectedItem:{}})}}>
                Add New
              </Button>
            </Row>
            <Colxx xxs="12">
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    List of Product Option Values
                  </CardTitle>
                  <Table hover>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Name</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        productoptionvalues && productoptionvalues.map((item,index)=>{
                          return(
                            <tr
                            key = {"operator_" + index}
                            style = {{cursor:'pointer'}}
                            onClick={()=>{this.setState({modalOpen:true, selectedItem:item})}}
                            >
                              <th scope="row">{item._id}</th>
                              <td>{item.optionvalue_title}</td>
                              <td>
                                <div className="delete-operator">
                                  <i className="simple-icon-trash" onClick={this.showDeleteModal.bind(this,item)}/> 
                                </div>
                              </td>
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Colxx>
             <Modal isOpen={this.state.showDeleteModalFlag} toggle={this.toggle}>
                    <ModalHeader toggle={this.toggle}>
                      Warning
                      {/* <IntlMessages id="modal.modal-title" /> */}
                    </ModalHeader>
                    <ModalBody>
                          Are you sure to delete this option value?
                    </ModalBody>
                    <ModalFooter>
                      <Button color="primary" onClick={()=>{this.toggle();this.deleteProductOptionValue()}}>
                        Yes
                      </Button>{" "}
                      <Button color="secondary" onClick={this.toggle}>
                        Cancel
                      </Button>
                    </ModalFooter>
                  </Modal>
            <AddNewProductOptionValue
              selectedItem = {selectedItem}
              modalOpen = {modalOpen}
              closeModal={this.closeModal}
              onChangeProperty={this.onChangeProperty.bind(this)}
              selectedProductOptionValue = {selectedProductOptionValue}
              onSubmit={this.onNewInstance.bind(this)}
              // categories={categories}
            />
            <Pagination
              currentPage={this.state.currentPage}
              totalPage={10}
              onChangePage={i => this.onChangePage(i)}
            />
          </Fragment>
        )
    }
}
