import gql from 'graphql-tag';
import GRAPHQL from './index'


export default class {
  
  
  static getProductOptions(callback) {
    const query = {
      query: gql`
        {
          saleGetProductOptions {
          status
          message
          success
          data
        }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetProductOptions.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static postProductOption(data,callback) {
    
    const query = {
      mutation: gql`
          mutation{
            salePostProductOption(        
              option_id:"${data._id?data._id:''}", 
              option_seller_id: "",
              option_identifier: "${data.option_identifier}",
              lang_data: ${data.lang_data},
            ){
              status
              message
              success
              data
            }
          }
        `
    }
    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostProductOption.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static deleteProductOption(option_id,callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteProductOption(
              option_id:"${option_id}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleDeleteProductOption.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

 
}
