import React from 'react';
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";

const BaseModal = props => {

  const {
    open,
    handleOpen,
    title,
    content,
    buttons,
    ...restProps
  } = props;

  const toggleOpen = () => handleOpen(!open);

  return (
    <Modal isOpen={open} toggle={toggleOpen} {...restProps}>
      <ModalHeader toggle={toggleOpen}>
        {title}
      </ModalHeader>
      <ModalBody>
        {content}
      </ModalBody>
      <ModalFooter>
        {buttons}
      </ModalFooter>
    </Modal>
  );
};

export default BaseModal;