import React, { Component, Fragment } from "react";
import {
  Row,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  Card,
  CardBody,
  CardTitle,
  ModalFooter,
  Table,
} from "reactstrap";

import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import AddNewUserModal from "../../../components/modals/AddNewUser";
import UserService from "../../../service/graphql/User";
import { NotificationManager } from "../../../components/common/react-notifications";


export default class Users extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: true,
      showDeleteModalFlag: false,
      selectedItem: {},
      selectedUser: {},
      users: [],
      currentPage: 1,
      pageSize: 10,
      totalPage: 0,
    };
  }
  componentDidMount() {
    this.refresh();
  }
  refresh() {
    UserService.getUsers({page: this.state.currentPage, pageSize: this.state.pageSize}, (response) => {
      if (response.status === 1) {
        console.log("response.data----------->>>>", response.data);
        this.setState({
          users: response.data.results,
          currentPage: response.data.page,
          pageSize: response.data.pageSize,
          totalPage: Math.ceil(response.data.total / response.data.pageSize)
        });
        // console.log("response.data===>",response.data)
      }
    });
  }
  onChangePage(i) {
    this.setState({ currentPage: i }, this.refresh);    
  }
  closeModal = () => {
    this.setState({
      modalOpen: false,
    });
  };
  showModal = () => {
    this.setState({
      modalOpen: true,
    });
  };
  toggle = () => {
    this.setState({
      showDeleteModalFlag: false,
    });
  };
  showDeleteModal(item, e) {
    e.stopPropagation();
    e.preventDefault();
    this.setState({ showDeleteModalFlag: true, selectedItem: item });
  }
  deleteUser() {
    const { selectedItem } = this.state;
    console.log("selectedItem ..... on ----L>", selectedItem);
    UserService.deleteUser(selectedItem._id, (error, res) => {
      this.refresh();
    });
  }
  onNewInstance(e) {
    const { selectedItem } = this.state;
    if (
      selectedItem.full_name === undefined ||
      selectedItem.full_name.length === 0 ||
      selectedItem.wallet_address === undefined ||
      selectedItem.wallet_address.length === 0
    ) {
      NotificationManager.error(
        "Error message",
        "Please confirm valid data!",
        3000,
        () => {
          // alert("callback");
        },
        null,
        "error"
      );
      return;
    }
    UserService.postUser(this.state.selectedItem, (error, res) => {
      this.closeModal();
      this.refresh();
    });
  }
  onChangeProperty(value, type) {
    const { selectedItem } = this.state;
    if (type === "image") {
      let reader = new FileReader();
      reader.readAsDataURL(value);
      reader.onloadend = () => {
        selectedItem[type] = value;
        this.setState({ selectedItem });
      };
    } else {
      selectedItem[type] = value;
      this.setState({ selectedItem });
    }
  }
  render() {
    const { modalOpen, selectedUser, users, selectedItem } = this.state;
    return (
      <Fragment>
        <Row>
          <Colxx xxs="12">
            <Breadcrumb heading="Users" match={this.props.match} />
            <Separator className="mb-5" />
          </Colxx>
        </Row>
        <Row className="display-flex flex-row-reverse mb-4 mr-4">
          <Button
            color="primary"
            size="lg"
            className="top-right-button"
            onClick={() => {
              this.showModal();
              this.setState({ selectedItem: {} });
            }}
          >
            Add New
          </Button>
        </Row>
        <Colxx xxs="12">
          <Card className="mb-4">
            <CardBody>
              <CardTitle>List of Users</CardTitle>
              <Table hover>
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Full Name</th>
                    <th>Wallet Address</th>
                    {/* <th>Country Code</th> */}
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {users &&
                    users.map((item, index) => {
                      return (
                        <tr
                          key={"operator_" + index}
                          style={{ cursor: "pointer" }}
                          onClick={() => {
                            this.setState({
                              modalOpen: true,
                              selectedItem: item,
                            });
                          }}
                        >
                          <th scope="row">{item._id}</th>
                          <td>{item.full_name}</td>
                          <td>{item.wallet_address}</td>
                          {/* <td>{item.country_code}</td> */}
                          <td>
                            <div className="delete-operator">
                              <i
                                className="simple-icon-pencil"
                                onClick={() => {
                                  this.setState({
                                    modalOpen: true,
                                    selectedItem: item,
                                  });
                                }}
                              />
                              <i
                                className="simple-icon-trash ml-3"
                                onClick={this.showDeleteModal.bind(this, item)}
                              />
                            </div>
                          </td>
                        </tr>
                      );
                    })}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Colxx>
        <Modal isOpen={this.state.showDeleteModalFlag} toggle={this.toggle}>
          <ModalHeader toggle={this.toggle}>
            Warning
            {/* <IntlMessages id="modal.modal-title" /> */}
          </ModalHeader>
          <ModalBody>Are you sure to delete this user?</ModalBody>
          <ModalFooter>
            <Button
              color="primary"
              onClick={() => {
                this.toggle();
                this.deleteUser();
              }}
            >
              Yes
            </Button>{" "}
            <Button color="secondary" onClick={this.toggle}>
              Cancel
            </Button>
          </ModalFooter>
        </Modal>
        <AddNewUserModal
          selectedItem={selectedItem}
          modalOpen={modalOpen}
          closeModal={this.closeModal}
          onChangeProperty={this.onChangeProperty.bind(this)}
          selectedUser={selectedUser}
          onSubmit={this.onNewInstance.bind(this)}
          parent={this}
          // categories={categories}
        />
        <Pagination
          currentPage={this.state.currentPage}
          totalPage={this.state.totalPage}
          onChangePage={(i) => this.onChangePage(i)}
        />
      </Fragment>
    );
  }
}
