import React from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Form
  
} from "reactstrap";

import "react-datepicker/dist/react-datepicker.css";
import IntlMessages from "../../helpers/IntlMessages";

const AddNewModal = ({ modalOpen,selectedItem, toggleModal,onSubmit,closeModal,onChangeProperty }) => {
  const {start_date,end_date} = selectedItem
  const handleSubmit = (e)=>{
    e.stopPropagation();
    e.preventDefault();
    onSubmit();
  }
  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
          {selectedItem._id?"Edit Language":"Add New Language"}
      </ModalHeader>
      <Form onSubmit={handleSubmit}>
      <ModalBody>
        <Label>
            Code 
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'lang_code')}} value={selectedItem.lang_code}/>
        <Label className="mt-4">
            Name
        </Label>
        <Input  required onChange={(e)=>{onChangeProperty(e.target.value,'lang_name')}} value={selectedItem.lang_name}/>
      </ModalBody>
      
      <ModalFooter>
        <Button color="secondary" outline onClick={closeModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button color="primary" type="submit">
          <IntlMessages id="pages.submit" />
        </Button>{" "}
      </ModalFooter>
      </Form>
    </Modal>
  );
};

export default AddNewModal;
