import times from '../data/times';

/**
 * Get delivery time string from start end and end time
 * 
 * @param {string} startTime - the format is 'HH:MM'
 * @param {string} endTime - the format is 'HH:MM'
 * 
 * return - the format is 'HH:MM AA' - 'HH:MM AA'
 */
export const getDeliveryTimeFromHubTiming = (startTime, endTime) => {
  let deliveryTime = '';

  if (startTime && endTime) {

    const start = times.find(tm => tm.value === startTime) || null;
    const end = times.find(tm => tm.value === endTime) || null;

    deliveryTime = `${start?.label || ''} - ${end?.label || ''}`;
  }

  return deliveryTime;
};