import React from 'react';
import { Table } from 'reactstrap';
import { get } from 'lodash';
import moment from 'moment';
import {
  DATA_FORMAT,
  TABLE_ITEM_TYPES,
} from '../../../constants/defaultValues';

const BaseTable = props => {

  const {
    data,
    columns,
    actions,
    ...restProps
  } = props;

  return (
    <Table
      className={data && data.length ? 'table-responsive w-100' : ''}
      {...restProps}
    >
      <thead>
        <tr>
          {columns.map((item, id) => {
            return (
              <th
                key={item.name + id}
                className={item.className || ''}
                style={item.style || {}}
              >
                <span>{item.name}</span>
              </th>
            );
          })}
          {actions && <th>Action</th>}
        </tr>
      </thead>
      <tbody>
        {data &&
          data.length > 0 &&
          data.map((item, index) => {
            return (
              <tr key={item + index}>
                {columns.map((tableItem, id) => {
                  const itemValue = get(item, tableItem.key) || '';

                  return (
                    <td
                      key={tableItem.key + id}
                      className={tableItem.className || ''}
                      style={tableItem.style || {}}
                    >
                      <span>
                        {tableItem.type === TABLE_ITEM_TYPES.date && (itemValue ? moment(itemValue).format(DATA_FORMAT) : '-')}
                        {tableItem.type === TABLE_ITEM_TYPES.boolean && itemValue.toString()}
                        {tableItem.type === undefined && (itemValue || '-')}
                      </span>
                    </td>
                  )
                })}
                {actions && <td key={'actions_' + index}>{actions(item, index)}</td>}
              </tr>
            );
          })}
      </tbody>
    </Table>
      
  )
};

export default BaseTable;

