import React, { Component, Fragment } from "react";
import {
  Row, Button, Modal, ModalHeader,
  ModalBody, Card, CardBody, CardTitle, Table, CardSubtitle,
  ModalFooter, CustomInput, DropdownToggle
} from "reactstrap";
import IntlMessages from "../../../helpers/IntlMessages";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import TitleRouter from "../../../containers/navs/TitleRouter";
import Pagination from "../../../components/pages/Pagination";
import DataListView from "../../../components/pages/DataListView";
import AddNewModal from "../../../components/modals/AddNewCategory";
import AddNewShopDays from "../../../components/modals/AddNewShopDays";
import CategoryService from '../../../service/graphql/Category'
import PromotionService from '../../../service/graphql/Promotion'
import { NotificationManager } from "../../../components/common/react-notifications";
