// import ListPageHeading from '../../components/pages/ListPageHeading'

export default class Categories extends Component {
  constructor(props) {
    super(props)
    this.state = {
      modal: true,
      showDeleteModal: false,
      items: [
        { id: 1, name: "Meat", type: "type", description: "description", category: 'category' },
        { id: 2, name: "Deli", description: "description", category: 'category' },
        { id: 3, name: "Seafood", description: "description", category: 'category' },
        { id: 4, name: "Bakery", description: "description", category: 'category' },
        { id: 5, name: "Pantry", description: "description", category: 'category' },
      ],
      selectedPromotion: {},
      selectedItem: {},
      categories: [],
      currentPage: 1,
      categoryName: '',
      totalCategories: {},
      img: "/assets/check-symbol.png",
      title: "",
      categoryList: [],
      path: [{ name: 'Home', id: '', list: {} }, { name: 'Categories', id: '', list: {} }],
      startDate: null,
      startDateTime: null,
      startDateRange: null,
      endDateRange: null,
      embeddedDate: moment(),
      highlightWithRanges: [],
      inline: true,
      availableWeekday: true,
      updatePageCnt: 0,
      categoryArray:[],
      pageSize:10,
      totalPage: 0,
    }
  }
  componentDidMount() {
    this.refresh(this.state.currentPage);
    this.getListCategories()
    this.setStartDate()
  }

  convertChild(obj) {
    const {categoryArray} = this.state
    categoryArray.push(obj)
    const que = [obj];
    while (que.length > 0) {
      const current = que.shift();
      if (Object.keys(current.childs).length > 0) {
        const childs = Object.keys(current.childs).map(o => current.childs[o]);
        // obj.childs = childs;
        que.push(...childs);
        if (childs.length>0){
          childs.map((item)=>{
            categoryArray.push(item)
          })
        }
      }
    }
    this.setState({categoryArray})
  }

  refresh(currentPage) {
    var result = [];
    var id = ''
    var {path}=this.state
    var categoryItem={}
    const params = {
      page: currentPage,
      pageSize: 5,
      langCode: "en",
      pageSize:this.state.pageSize,
      page:this.state.currentPage
    };
    CategoryService.getCategories(params, response => {
      if (response.status === 1) {
        const categories = response.data.results;
        console.log("categories.....",categories)
        this.setState({ totalCategories: categories })
        for (const id of Object.keys(categories)) {
          this.convertChild(categories[id]);
          result.push(categories[id]);
        }
        const totalPage = Math.ceil(response.data.total / response.data.pageSize);
        this.setState({ categories: result, currentPage: response.data.page, pageSize: response.data.pageSize, totalPage: totalPage })
      } else {
      }
    })
  }
  refreshPage() {
    var result = [];
    var id = ''
    var {path}=this.state
    var categoryItem={}
    const params = {
      langCode: "en",
      pageSize:this.state.pageSize,
      page: this.state.currentPage
    };
    console.log("categories refresh", params)
    CategoryService.getCategories(params, response => {
      if (response.status === 1) {
        const categories = response.data.results;

        this.setState({ totalCategories: categories })
        for (const id of Object.keys(categories)) {
          this.convertChild(categories[id]);
          result.push(categories[id]);
        }
        const totalPage = Math.ceil(response.data.total / response.data.pageSize);
        this.setState({ categories: result, pageSize: response.data.pageSize, totalPage: totalPage })
      } else {
      }
    })
  }
  getListCategories = (inputValue, callback) => {
    var categoryList = [];
    const params = {
      parentId: "",
      keyword: "",
      langCode: "en"
    };
    CategoryService.getListCategories(params, response => {
      if (response.status === 1) {
        const optionsTmp = []
        categoryList = response.data
        categoryList.forEach(element => {
          const obj = {
            label: element.name,
            value: element.prodcat_id,
            id: element.prodcat_id,
          }
          optionsTmp.push(obj)
        });
        this.setState({ categoryList: optionsTmp })
      } else {
      }
    })
  };
  onChangePage(i) {
    console.log('pagenation...', i)
    this.setState({ currentPage: i }, this.refreshPage)    
  }
  setStartDate() {
    var startDate = new Date()
    startDate = moment(startDate)
    this.setState({ startDate })
  }
  closeModal = () => {
    this.setState({
      modalOpen: false
    });
  };
  showModal = () => {
    this.setState({
      modalOpen: true
    });
  };
  toggleModal = () => {
    this.setState({
      modalOpen: !this.state.modalOpen
    });
  };
  toggle = () => {
    this.setState({
      showDeleteModal: !this.state.showDeleteModal
    });
  }
  deleteCategory() {
    const { selectedItem } = this.state
    CategoryService.deleteCategory(selectedItem._id, (error, res) => {
      this.setState({currentPage:1})
      this.refresh(1); 
    })
  }
  onSubmit(e) {
    const { selectedItem } = this.state
    const langDataTmp = [
      {
        lang_code: "en",
        title: "Clothing",
        description: ""
      },
      {
        lang_code: "zh",
        title: "衣服",
        description: ""
      }
    ]
    if (selectedItem.prodcat_identifier === undefined || selectedItem.prodcat_identifier === '') {
      NotificationManager.error(
        "Error message",
        "Please confirm valid data!",
        3000,
        () => {
          // alert("callback");
        },
        null,
        "error"
      );
      return;
    }
    let langData = '['
    for (let i = 0; i < langDataTmp.length; i++) {
      langData += `{lang_code: "${langDataTmp[i].lang_code}",title: "${langDataTmp[i].title}",description: "${langDataTmp[i].description}"},`
    }
    langData += ']'
    const params = {
      prodcatId: selectedItem._id ? selectedItem._id : '',
      prodcatParent: selectedItem.prodcat_parent ? selectedItem.prodcat_parent : '',
      prodcatActive: selectedItem.prodcat_active ? true : false,
      prodcatFeatured: selectedItem.prodcat_featured ? true : false,
      prodcatIdentifier: selectedItem.prodcat_identifier ? selectedItem.prodcat_identifier : '',
      prodcatDisplayOrder: selectedItem.prodcat_display_order ? selectedItem.prodcat_display_order : '',
      langData: langData
    }
    CategoryService.postCategory(params, (error, res) => {
      this.closeModal()
      this.setState({currentPage:1})
      this.refresh(1); 
    })
  }
  newRouter = (name, id, list) => {
    console.log('newRouter...', name, id, list)

    var path = this.state.path
    if (name === 'Home') {
      this.props.history.push(`/app`)
    }
    else if (name === 'Categories') {
      // this.listRefresh(this.state.totalCategories)
      var path = path.slice(0, 2);
      this.setState({ path })
      this.setState({categoryArray:[]})
      this.props.history.push({
        pathname: `/app/categories/list`,
      })
      this.setState({currentPage:1})
      this.refresh(1); 
    }
    else {
      this.listRefresh(list)
    }
  }
  onChangeProperty = (value, type) => {
    console.log("e--->>>", value, type)
    const { selectedItem } = this.state
    if (type === 'prodcat_active' || type === 'prodcat_featured') {
      selectedItem[type] = !selectedItem[type]
    }
    else {
      selectedItem[type] = value
    }
    this.setState({ selectedItem })
  }
  showDeleteModal(item, e) {
    e.stopPropagation();
    e.preventDefault();
    this.setState({ showDeleteModal: true, selectedItem: item })

  }
  handleCheckChange = (event, id) => {
    event.stopPropagation();
    event.preventDefault();
  }
  onGoSubCategoryPage(item, e) {
    e.stopPropagation();
    e.preventDefault();

  }
  listRefresh(item) {
    console.log('item...1', item)
    // this.props.history.push(`/app/categories/categoryDetail/${item._id}`);
    var pathTmp = this.state.path
    var num = (pathTmp.findIndex(o => o.id == item._id))
    if (num > 0) {
      var pathTmp = pathTmp.slice(0, num + 1);
    }
    else {
      pathTmp.push({ name: item.prodcat_identifier, id: item._id, list: item })
    }
    var categoriesTmp = [];
    var id = ''
    this.setState({ path: pathTmp })
    console.log('path...', pathTmp)
    const childIitem = item.childs ? item.childs : null
    const categoryName = item.prodcat_identifier ? item.prodcat_identifier : ''
    if (childIitem) {
      for (id of Object.keys(childIitem)) {
        categoriesTmp.push(childIitem[id]);
      }
      this.setState({ categories: categoriesTmp, categoryName })
    }
  }
  handleChangeDate = date => {
    var { inline, highlightWithRanges, updatePageCnt } = this.state
    // inline=true
    var highlightWithRangesTmp = moment(new Date(date))
    highlightWithRanges.push(highlightWithRangesTmp)
    updatePageCnt = updatePageCnt + 1
    this.setState({ inline, updatePageCnt, highlightWithRanges })
    // this.setState({startDate: date})
  }
  subDays(date, days) {
    var result = new Date(date);
    result.setDate(result.getDate() - days);
    return result;
  }
  addDays(date, days) {
    var result = new Date(date);
    result.setDate(result.getDate() + days);
    return moment(result);
  }
  handleMonthChange = date => {
  }

  render() {
    const { selectedItem, modalOpen, categories, categoryName, categoryList, path, inline } = this.state
    return (
      <Fragment>
        <Row >
          <Colxx xxs="12">
            <TitleRouter
              heading="Categories"
              match={path}
              newRouter={this.newRouter}
            />
            {/* <Breadcrumb heading="Categories" 
                match={this.props.match} 
                /> */}
            <Separator className="mb-5" />
          </Colxx>
        </Row>
        <Row className="display-flex flex-row-reverse mb-4 mr-4">
          <NavLink to={{
            pathname: `/app/categories/category_detail/0`,
            state: { selectedItem: {}, categoryName: '', categoryList: categoryList }
          }}>
            <Button
              color="primary"
              size="sm"
              className="top-right-button"
              onClick={() => { this.setState({ selectedItem: {}, categoryName: '' }) }}>
              <IntlMessages id="pages.add-new" />
            </Button>
          </NavLink>

        </Row>
        <Colxx>
          <Card className="mb-4">
            <CardBody>

              {/* <Separator className="mb-5" /> */}
              <CardTitle>
                List
                  </CardTitle>
              <Table>
                <thead>
                  <tr>
                    <th>Id</th>
                    <th>Identifier</th>
                    {/* <th>Sub Category</th> */}
                    <th>Subcategory</th>
                    <th>Category Order</th>
                    <th>Active</th>
                    <th>Featured</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {
                    (categories && categories.length > 0) && categories.map((item, index) => {
                      return (
                        <tr key={index}>
                          <th scope="row">{item._id}</th>
                          <td>{item.prodcat_identifier}</td>
                          <td style={{ cursor: 'pointer' }} onClick={this.listRefresh.bind(this, item)}>
                            <i className="simple-icon-layers" />
                            <span style={{ marginLeft: '5px' }} className="align-middle d-inline-block">{item.childs ? Object.entries(item.childs).length : 0}</span>
                          </td>
                          <td>{item.prodcat_display_order}</td>
                          <td>
                            <div className="custom-control custom-checkbox pl-1 align-self-center mr-4">
                              <span>
                                <img style={{ width: '11px', height: '11px' }} alt="" src={item.prodcat_active ? "/assets/img/check-symbol.png" : '/assets/img/uncheck-symbol.png'} />
                              </span>
                              {/* <CustomInput
                                    className="itemCheck mb-0"
                                    type="checkbox"
                                    id={`check_${item._id}`}
                                    checked={item.prodcat_active}
                                    onChange={event => this.handleCheckChange(event, item.prodcat_id)}
                                    label=""
                                  /> */}
                            </div>
                          </td>
                          <td>

                            <div className="custom-control custom-checkbox pl-1 align-self-center mr-4">
                              <span>
                                <img style={{ width: '11px', height: '11px' }} alt="" src={item.prodcat_featured ? "/assets/img/check-symbol.png" : '/assets/img/uncheck-symbol.png'} />
                              </span>
                              {/* <CustomInput
                                    className="itemCheck mb-0"
                                    type="checkbox"
                                    id={`check_${item._id}`}
                                    checked={item.prodcat_featured}
                                    onChange={event => this.handleCheckChange(event, item.prodcat_id)}
                                    label=""
                                  /> */}
                            </div>
                          </td>
                          <td>
                            <div className="action-group">
                              <NavLink to={{
                                pathname: `/app/categories/category_detail/${item._id}`,
                                state: { selectedItem: item, categoryName: categoryName, categoryList: categoryList }
                              }}>
                                <i style={{ cursor: 'pointer' }} className="simple-icon-pencil"
                                />
                              </NavLink>
                              {/* <i style = {{cursor:'pointer'}} className="simple-icon-pencil" 
                                  onClick={()=>{
                                      this.setState({modalOpen:true,selectedItem:item,categoryName:categories[item._id]})
                                    }}
                                    /> */}
                              <i style={{ marginLeft: '10px' }} className="simple-icon-trash"
                                onClick={this.showDeleteModal.bind(this, item)}
                              />
                            </div>
                          </td>

                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table>

            </CardBody>
          </Card>
        </Colxx>
        <Modal isOpen={this.state.showDeleteModal} toggle={this.toggle}>
          <ModalHeader toggle={this.toggle}>
            Warning
                {/* <IntlMessages id="modal.modal-title" /> */}
          </ModalHeader>
          <ModalBody>
            Are you sure to delete this category?
              </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={() => { this.toggle(); this.deleteCategory() }}>
              Yes
                </Button>{" "}
            <Button color="secondary" onClick={this.toggle}>
              Cancel
                </Button>
          </ModalFooter>
        </Modal>
        <AddNewShopDays
          selectedItem={selectedItem}
          modalOpen={modalOpen}
          startDate={this.state.startDate}
          availableWeekday={this.state.availableWeekday}
          handleChangeDate={this.handleChangeDate}
          onMonthChange={this.handleMonthChange}
          highlightWithRanges={this.state.highlightWithRanges}
          placeholderText="Shop Special Days"
          inline={inline}
          updatePageCnt={this.state.updatePageCnt}
          closeModal={this.closeModal}
          onChangeProperty={this.onChangeProperty}
          onSubmit={this.onSubmit.bind(this)}
        />
        {this.state.categoryName === "" && <Pagination
          currentPage={this.state.currentPage}
          totalPage={this.state.totalPage}
          onChangePage={i => this.onChangePage(i)}          
        />}

      </Fragment>
    )
  }
}
