import React,{useEffect,useState} from "react";
import {
  CustomInput,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Row, 
  Form
} from "reactstrap";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import IntlMessages from "../../helpers/IntlMessages";
import moment from 'moment'
import Config from '../../service/api/config'
const AddNewModal = ({ modalOpen,selectedItem, toggleModal,onSubmit,closeModal,onChangeProperty }) => {
  const {start_date,end_date} = selectedItem
  const [imageChanged, setImageChanged] = useState(false)
  const handleSubmit = (e)=>{
    e.stopPropagation();
    e.preventDefault();
    if(!imageChanged){
        selectedItem.image = ""
    }
    onSubmit();
  }
  useEffect(()=>{

  },[])
  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
          {selectedItem._id?"Edit Promotion":"Add New Promotion"}
      </ModalHeader>
      <Form onSubmit={handleSubmit}>
      <ModalBody>
      <Label className="mt-4">
            Image
        </Label>
        <CustomInput
          type="file"
          id="exampleCustomFileBrowser1"
          name="customFile"
          onChange={(e)=>{setImageChanged(true); onChangeProperty(e.target.files[0],'image')}}
        />
        <div>
        {
          selectedItem.image && selectedItem.image.indexOf(".png") > -1 ?
          <img className="product-image" src={Config.BACKEND_API_URL + selectedItem.image}/>:
          <img className="product-image" src={selectedItem.image}/>
        }
        </div>
        <Label>
            Name
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'name')}} value={selectedItem.name}/>
        <Label className="mt-4">
            Discount Type
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'discount_type')}} value={selectedItem.discount_type}/>
        <Label className="mt-4">
            Discount Value
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'discount_value')}} value={selectedItem.discount_value}/>
        <Label className="mt-4">
            Promotion Type
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'promotion_type')}} value={selectedItem.promotion_type}/>
        <Label className="mt-4">
            Location
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'location')}} value={selectedItem.location}/>
       
        <Label className="mt-4">
            Enable Status
        </Label>
        <CustomInput
          required
          type="radio"
          id="exCustomRadio"
          name="customRadio"
          label="Enable"
          onClick={(e)=>{onChangeProperty(true,'enable_status')}} 
          checked = {selectedItem.enable_status}
          />
        <CustomInput
          required
          type="radio"
          id="exCustomRadio2"
          name="customRadio"
          label="Disable"
            checked = {!selectedItem.enable_status}
          onClick={(e)=>{onChangeProperty(false,'enable_status')}} value={selectedItem.enable_status}/>
        <Label className="mt-4">
            Special Code
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'special_code')}} value={selectedItem.special_code||""}/>
        <Row className="mr-1 ml-1 mt-4">
            <DatePicker
              required
              selected={moment(start_date*1000)}
              selectsStart
              startDate={moment(start_date*1000)}
              endDate={moment(end_date*1000)}
              onChange={(e)=>{onChangeProperty(e, 'start_date')}} 
              placeholderText="Start date"/>
        </Row>
        <Row className="mr-1 ml-1 mt-4">
            <DatePicker
              required
              selected={moment(end_date*1000)}
              selectsEnd
              startDate={moment(start_date*1000)}
              endDate={moment(end_date*1000)}
              onChange={(e)=>{onChangeProperty(e, 'end_date')}} 
              placeholderText="End date" />
        </Row>
      </ModalBody>
      
      <ModalFooter>
        <Button color="secondary" outline onClick={closeModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button color="primary"  type="submit">
          <IntlMessages id="pages.submit" />
        </Button>{" "}
      </ModalFooter>
      </Form>
    </Modal>
  );
};

export default AddNewModal;
