import React, { useEffect, useState, Fragment } from 'react';
import {
  Row,
  Card,
  CardBody,
  Input,
  Button,
  Label,
  Form,
} from 'reactstrap';
import AsyncSelect from 'react-select/async';
import Select from 'react-select';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

import IntlMessages from '../../../helpers/IntlMessages';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import CustomSelectInput from '../../../components/common/CustomSelectInput';
import { orderModel } from './models';
import { getDeliveryTimeFromHubTiming } from '../../../utils/utils';
import { EditableTable } from '../common';
import { ORDER_TIEMS_TABLE_ITEMS } from './constants';

import OrderServices from '../../../service/graphql/Order';
import UserServices from '../../../service/graphql/User';
import HubServices from '../../../service/graphql/Hub';
import moment from "moment";
import './style.css';

/**
 * Style for product select element
 */
const productItem = {
  container: styles => ({ ...styles, width: '60%' })
};

const OrderDetail = (props) => {
  const {
    match,
    match: {
      params: {
        id: orderId
      }
    },
    history
  } = props;

  const [isIniting, setIsIniting] = useState(true);
  const [order, setOrder] = useState({...orderModel});

  const [selectedUser, setSelectedUser] = useState(null);
  const [selectedHub, setSelectedHub] = useState(null);
  const [hubTimings, setHubTimings] = useState([]);
  const [selectedHubTiming, setSelectedHubTiming] = useState(null);
  const [products, setProducts] = useState([]);
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [selectedProductQuantity, setSelectedProductQuantity] = useState(0);
  const [orderItems, setOrderItems] = useState([]);
  const [deliveryDate, setDeliveryDate] = useState('');
  const [deliveryAddress, setDeliveryAddress] = useState('');
  const [addedNote, setAddedNote] = useState('');
  const [contactPhone, setContactPhone] = useState('');

  useEffect(() => {
    let selectedItem = { ...orderModel };

    if (orderId !== '0') {
      const query = {
        order_id: orderId,
      };
 
      OrderServices.saleGetOrder(query, res => {        
        if (res.status === 1) {
          selectedItem = res?.data || orderModel;
          let deliverydate = null;
          if (selectedItem.delivery_date){
            deliverydate = new Date(selectedItem.delivery_date)  
            deliverydate = moment(deliverydate);
          }     
          setOrder(selectedItem);
          setSelectedUser(selectedItem.user);
          setSelectedHub(selectedItem.hub);
          setSelectedHubTiming(selectedItem.timeslot_data);
          setOrderItems(selectedItem?.order_items.map(item => {
            return {
              seller_product_id: item?.seller_product_id || '',
              seller_product_identifier: item?.seller_product?.seller_product_identifier || '',
              quantity: item?.quantity || 0,
            }
          }));
          setDeliveryDate(deliverydate);
          setDeliveryAddress(selectedItem?.delivery_address || '');
          setAddedNote(selectedItem?.added_note || '');
          setContactPhone(selectedItem?.contact_phone || '');

          setIsIniting(false);
        }
      });
    } else {
      setOrder(selectedItem);
      setSelectedUser(null);
      setSelectedHub(null);
      setSelectedHubTiming(null);
      setOrderItems([]);
      setDeliveryDate('');
      setDeliveryAddress('');
      setAddedNote('');
      setContactPhone('');

      setIsIniting(false);
    }
  }, [orderId]);

  const loadUserOptions = (value, callback) => {
    UserServices.getUserOptions(value, res => {  
      if(res.status === 1) {
          const users = res.data.results;

          callback(users);
        }
    })
  };

  const loadHubOptions = (value, callback) => {
    const query = {
      page: 1,
      pageSize: 100,
    };

    HubServices.getHubs(query, res => {  
      if(res.status === 1) {
          const hubs = res.data.results;

          callback(hubs);
        }
    })
  };

  const asyncGetTiminges = async (hub) => {
    return new Promise((resolve, reject) => {
      const query = {
        _id: hub._id,
      };
      HubServices.getHubTimings(query, res => {
        if (res.status === 1) {
          resolve(res?.data?.hub_timings || []);
        } else {
          resolve([]);
        }
      });
    });
  };

  const getHubTimings = async (hub, init = false) => {
    if (hub) {
      const _hubTimings = await asyncGetTiminges(hub);

      setHubTimings(_hubTimings);

      if (!init) {
        setSelectedHubTiming(null);
      }
    }
  };

  const asyncGetProducts = async (hub) => {
    return new Promise((resolve, reject) => {
      const query = {
        hub_id: hub._id, 
        timeslot: '',
        delivery_date: '',
        keyword: '',
        shopId: '',
        sortBy: 'AtoZ', 
        page: 1, 
        category_id: '',
        pageSize: 10
      };
      OrderServices.saleGetProductsByFilters(query, res => {
        if (res.status === 1) {
          resolve(res?.data?.results || []);
        } else {
          resolve([]);
        }
      });
    });
  };

  const getProducts = async (hub) => {
    if (hub) {
      const _products = await asyncGetProducts(hub);

      const sellerProducts = [];
      // eslint-disable-next-line no-unused-expressions
      _products?.map(_prod => {
        Object.entries(_prod?.options).forEach(([key, value]) => {          
          sellerProducts.push({
            seller_product_id: value.seller_product_id,
            seller_product_identifier: value.seller_product_identifier,
          });
        });
      });
      setProducts(sellerProducts);
      setSelectedProduct(null);
    }
  };

  const getTimeslotLabel = timeslot => {
    return getDeliveryTimeFromHubTiming(
      timeslot?.delivery_start_time || '',
      timeslot?.delivery_end_time || ''
    );
  };

  useEffect(() => {
    getHubTimings(selectedHub, isIniting);
    getProducts(selectedHub);
    
    if (!isIniting) {
      setOrderItems([]);
      setProducts([]);
      setSelectedProductQuantity(0);
      setSelectedProduct(null);
    }
  }, [selectedHub]);

  const gotoOrders = () => {
    history.push(`/app/orders/list`);
  }

  const onChangeProductQuantity = e => {
    if (e.target.value >= 0) {
      setSelectedProductQuantity(e.target.value);
    }
  };
  
  const addNewProdcut = () => {
    if (selectedProduct && selectedProductQuantity) {
      const item = {
        seller_product_id: selectedProduct.seller_product_id,
        seller_product_identifier: selectedProduct.seller_product_identifier,
        quantity: selectedProductQuantity
      };
  
      const _orderItems = [...orderItems, item];
      setOrderItems(_orderItems);
    }
  }

  const handleDeleteOrderItem = id => {
    setOrderItems(orderItems.filter((item, index) => index !== id));
  }

  const onChangeOrderItem = (e, index, key) => {
    setOrderItems(orderItems.map((item, id) => {
      if (index === id) {
        return {
          ...item, 
          [key]: e.target.value, 
        }
      } else {
        return item;
      }
    }));
  };

  const handleDateChange = (date) => {
    setDeliveryDate(date);
  }

  const handleSubmit = () => {
    let selProdsData = '[';
    for (let j = 0; j < orderItems.length; j++) {
      selProdsData += `{selprod_id: "${orderItems[j].seller_product_id}", quantity:${orderItems[j].quantity}},`
    }
    selProdsData += ']';    
    const query = {
      order_id: typeof order._id != "undefined" ? order._id : '',
      user_id: selectedUser._id,
      selProds: selProdsData,
      delivery_date: deliveryDate ? deliveryDate.format('YYYY-MM-DD') : '',
      timeslot: selectedHubTiming._id,
      delivery_address: deliveryAddress,
      added_note: addedNote,
      contact_phone: contactPhone,
    }

    OrderServices.salePostOrder(query, res => {
      if (res.status === 1){
        gotoOrders();
      } else {
        alert('error on submit');
      }           
    });
  };

  return (
    <Fragment>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb
            heading={orderId === "0" ? 'New Order' : 'Order Detail'}
            match={match}
          />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Form onSubmit={handleSubmit}>
        <Row className="mb-4">
          <Colxx xxs="12" md="12">
            <Card>
              <CardBody className="d-flex flex-column">
                <Row>
                  <div className="flex-1">
                    <Label>User</Label>
                    <AsyncSelect
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      name="form-field-name"
                      loadOptions={loadUserOptions}
                      defaultOptions
                      value={selectedUser}
                      getOptionLabel={item => item.full_name || '-'}
                      getOptionValue={item => item._id}
                      onChange={e => setSelectedUser(e)}
                    />
                    <Label className="mt-4">Hub</Label>
                    <AsyncSelect
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      name="form-field-name"
                      loadOptions={loadHubOptions}
                      defaultOptions
                      value={selectedHub}
                      getOptionLabel={item => item.hub_identifier || '-'}
                      getOptionValue={item => item._id}
                      onChange={e => setSelectedHub(e)}
                    />
                    <Label className="mt-4">Time Slot</Label>
                    <Select
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      name="form-field-name"
                      options={hubTimings}
                      defaultOptions
                      value={selectedHubTiming}
                      getOptionLabel={item => getTimeslotLabel(item)}
                      getOptionValue={item => item._id}
                      onChange={e => setSelectedHubTiming(e)}
                    />
                    <Label className="mt-4">Order Items</Label>
                    <EditableTable
                      data={orderItems}
                      columns={ORDER_TIEMS_TABLE_ITEMS}
                      onChange={onChangeOrderItem}
                      actions={(item, index) => {
                        return (
                          <div>
                            <i
                              className="simple-icon-trash d-flex mt-2 justify-content-center"
                              style = {{ cursor:'pointer' }}
                              onClick={() => handleDeleteOrderItem(index)}
                            />
                          </div>
                        );
                      }}
                    />
                    {selectedHub && products && products.length > 0 && (
                      <Row className="w-100 m-0">
                        <Select
                          components={{ Input: CustomSelectInput }}
                          className="react-select"
                          name="form-field-name"
                          options={products}
                          defaultOptions
                          value={selectedProduct}
                          getOptionLabel={item => item.seller_product_identifier}
                          getOptionValue={item => item.seller_product_id}
                          onChange={e => setSelectedProduct(e)}
                          styles={productItem}
                        />
                        <Input
                          onChange={onChangeProductQuantity}
                          value={selectedProductQuantity}
                          type="number"
                          style={{
                            width: '20%',
                            marginLeft: '0.5rem',
                            padding: '0.25rem'
                          }}
                        />
                        <Button 
                          color="primary"
                          onClick={addNewProdcut}
                          className="ml-2 py-2 px-3 ml-auto"
                          disabled={selectedProductQuantity <= 0 || !selectedProduct}
                        >
                          Add
                        </Button>
                      </Row>
                    )}
                  </div>
                  <div className="flex-1">
                    <Label>Delivery Address</Label>
                    <Input
                      required
                      onChange={(e) => setDeliveryAddress(e.target.value)}
                      value={deliveryAddress || ''}
                      style={{ padding: '0.54rem' }}
                    />
                    <Label className="mt-4">Delivery Date</Label>
                    <DatePicker
                      required
                      selected={deliveryDate}
                      onChange={handleDateChange}
                      className="date-picker"
                    />
                    <Label className="mt-4">Contact Phone</Label>
                    <Input
                      required
                      onChange={(e) => setContactPhone(e.target.value)}
                      value={contactPhone || ''}
                      style={{ padding: '0.54rem' }}
                    />
                    <Label className="mt-4">Added Note</Label>
                    <Input
                      required
                      onChange={(e) => setAddedNote(e.target.value)}
                      value={addedNote || ''}
                      style={{ padding: '0.54rem' }}
                    />
                  </div>
                </Row>
                <Row className="m-2">
                  <Button 
                    color="primary"
                    onClick={handleSubmit}
                    className="mr-2"
                  >
                    <IntlMessages id="forms.submit" />
                  </Button>
                  <Button
                    color="secondary"
                    outline
                    onClick={gotoOrders}
                  >
                    <IntlMessages id="forms.close" />
                  </Button>
                </Row>
              </CardBody>
            </Card>
          </Colxx>
        </Row>
      </Form>
    </Fragment>
  );
};

export default OrderDetail;
