import gql from 'graphql-tag';
import GRAPHQL from './index'
import moment from 'moment'

export default class {

  static getCategories(params, callback) {
    const query = {
      query: gql`
        {
          saleGetAllCategories(page: ${params.page}, pageSize:${params.pageSize}, lang_code:"${params.langCode}") {
            status
            message
            success
            data
          }
        }
      `,
    };
    GRAPHQL.doQuery(query)
    .then(response => {
      try {
        return callback({
          status: 1,
          data: JSON.parse(response.data.saleGetAllCategories.data)
        })
      } catch (e) {
        return callback({ status: 0, message: "HTTPERROR" })
      }
    })
    .catch(error => {
      return callback({ status: 0, message: error })
    })
  }

  static getListCategories(params, callback) {
    const query = {
      query: gql`
        {
          saleListCategories(parent_id: "${params.parentId}", keyword:"${params.keyword}", lang_code: "${params.langCode}") {
            status
            message
            success
            data
          }
        }
      `,
    };
    GRAPHQL.doQuery(query)
    .then(response => {
      try {
        return callback({
          status: 1,
          data: JSON.parse(response.data.saleListCategories.data)
        })
      } catch (e) {
        return callback({ status: 0, message: "HTTPERROR" })
      }
    })
    .catch(error => {
      return callback({ status: 0, message: error })
    })
  }

  static getCategory(params, callback) {
    const query = {
      query: gql`
        {
          saleGetCategory(category_id: "${params.categoryId}") {
            status
            message
            success
            data
          }
        }
      `,
    };
    GRAPHQL.doQuery(query)
    .then(response => {
      try {
        return callback({
          status: 1,
          data: JSON.parse(response.data.saleGetCategory.data)
        })
      } catch (e) {
        return callback({ status: 0, message: "HTTPERROR" })
      }
    })
    .catch(error => {
      return callback({ status: 0, message: error })
    })
  }
  
  static postCategory(data,callback) {
    console.log('postCategoryData...req',data)
    const query = {
      mutation: gql`
          mutation{
            salePostCateogry(
              prodcat_id:"${data.prodcatId}",
              prodcat_parent:"${data.prodcatParent}", 
              prodcat_active: ${data.prodcatActive}, 
              prodcat_image: "${data.prodcatImage}",
              prodcat_featured: ${data.prodcatFeatured}, 
              prodcat_identifier:"${data.prodcatIdentifier}",
              prodcat_display_order: ${data.prodcatDisplayOrder},                        
              lang_data:${data.langData}
            ) {
              status
              message
              success
              data
            }
          }
        `
    }
    GRAPHQL.doMutate(query)
      .then(response => {
        console.log("response....",response)
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostCateogry.data)
          })
        } catch (e) {
          console.log("e on mutate...",e)
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static deleteCategory(id,callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteCategory(
              prodcat_id:"${id}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        console.log("response....",response)
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleDeleteCategory.data)
          })
        } catch (e) {
          console.log("e on mutate...",e)
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

 
}
