import React, { Component, Fragment } from "react";
import { Row, Button, Card, CardBody, Table ,   Label,CardText} from "reactstrap";
import moment from "moment";

import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import APIServices from "../../../service/api/api";

import { DATA_FORMAT_WITH_SECOND } from "../../../constants/defaultValues";
import { Link } from "react-router-dom";
import LoginRecordsService from '../../../service/graphql/LoginRecords'
var Loader = require('react-loader');

export default class caribbeanCricket extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPage: 1,
      caribbeanCricket: [],
      totalPage: 0,
      pageSize: 10,
      total: 0,
      accepted: 0,
      rejected: 0,
      loaded: true
    };
  }
  componentDidMount() {
    this.refresh(this.state.currentPage)
  }

  refresh = (currentPage) => {
    this.setState({loaded: false})
    const page = currentPage;
    const size = this.state.pageSize;
    LoginRecordsService.getLoginRecords(page, size, response => {
      if (response.status === 1) {
        const caribbeanCricket = [];
        const caribbeanCricketList = response.data.data;
        caribbeanCricketList.map((item, index) => {
          const userInfo = JSON.parse(item.userinfo)
          const itemData = {
            fullName: userInfo.full_name,
            updatedTimestamp: item.updated_timestamp,
            result: item.result == 1 ? 'Accepted' : 'Rejected',
            email: userInfo.email
          }
          caribbeanCricket.push(itemData)
        })

        this.setState({
          caribbeanCricket,
          totalPage: Math.ceil(response.data.total / this.state.pageSize),
          total: response.data.total, 
          accepted: response.data.total_accepted,
          rejected: response.data.total_rejected,
        });
      } else {
      }
      this.setState({loaded: true})
    })
  }

  onChangePage(i) {
    this.setState({ currentPage: i });
    setImmediate(() => {
      const { currentPage } = this.state;
      this.refresh(currentPage);
    });
  }
  render() {
    const { caribbeanCricket, loaded } = this.state;
    console.log("this.props: ", this.props);
    return (
      <Fragment>
        <Row>
          <Colxx xxs="12">
            <Breadcrumb heading="Caribbean Cricket" match={this.props.match} />
            <Separator className="mb-5" />
          </Colxx>
        </Row>
        <Loader loaded={loaded}>
        <Row className="row mx-3 mb-3 display-flex flex-row align-items-center">
          <Label className="mb-0 ml-3">
            Total:
          </Label>
          <CardText className="mb-0 ml-3">
           {this.state.total},
          </CardText>
          <Label className="mb-0 ml-3">
            Accepted:
          </Label>
          <CardText className="mb-0 ml-3">
          {this.state.accepted},
          </CardText>
          <Label className="mb-0 ml-3">
            Rejected:
          </Label>
          <CardText className="mb-0 ml-3">
          {this.state.rejected}
          </CardText>
        </Row>
        <Colxx>
          <Card className="mb-4">
            <CardBody>
              {/* <CardTitle>
                    List of prefunds
                  </CardTitle> */}
              <Table>
                <thead>
                  <tr>
                    <th>Full Name</th>
                    <th>Email</th>
                    <th>Result</th>
                    <th>Updated At</th>
                  </tr>
                </thead>
                <tbody>
                  {caribbeanCricket &&
                    caribbeanCricket.length > 0 &&
                    caribbeanCricket.map((item, index) => {
                      return (
                        <tr key={index}>
                          <th style={{ verticalAlign: "middle" }} scope="row">
                            {item.fullName}
                          </th>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.email}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.result}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.updatedTimestamp
                              ? moment(item.updatedTimestamp).format(
                                DATA_FORMAT_WITH_SECOND
                              )
                              : "-"}
                          </td>
                        </tr>
                      );
                    })}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Colxx>
        <Pagination
          currentPage={this.state.currentPage}
          totalPage={this.state.totalPage}
          onChangePage={(i) => this.onChangePage(i)}
        />
        </Loader>
      </Fragment>
    );
  }
}
