import React, { useMemo } from 'react';
import {
  Button,
  Row,
} from 'reactstrap';
import Steps from 'rc-steps';
import moment from 'moment';

import '../../../../node_modules/rc-steps/assets/index.css';
import './style.css';

import { BaseModal } from '../common';
import { ORDER_STEPS, ORDER_STATUS, STEP_STATUS } from './constants';
import { DATA_FORMAT_WITH_TIME } from '../../../constants/defaultValues';
import Config from '../../../service/api/config'
const OrderView = props => {
  const {
    open,
    handleOpen,
    data,
    ...restProps
  } = props;

  const getStepStatus = (curStatus, curStep) => {
    console.log('curStatus, curStep: ', curStatus, curStep);
    if (curStatus < curStep) {
      if (curStatus + 1 === curStep) {
        return STEP_STATUS.process;
      } else {
        return STEP_STATUS.wait;
      }
    } else {
      return STEP_STATUS.finish;
    }
  };

  const getPendingStepData = _statusOrderStep => {
    const _title = ORDER_STEPS.pending.title;
    const _status = getStepStatus(_statusOrderStep, ORDER_STEPS.pending.id);
    let _description = '';

    if (_status === STEP_STATUS.finish) {
      _description = (
        <div className="d-flex flex-column">
          <span>{data.created_at ? moment(data.created_at).format(DATA_FORMAT_WITH_TIME) : '-'}</span>
          <span>Created by {data?.user?.full_name || '-'}</span>
        </div>
      );
    } else {
      _description = '';
    }

    return {
      title: _title,
      description: _description,
      status: _status,
    };
  };

  const getColletedStepData = _statusOrderStep => {
    const _title = ORDER_STEPS.collected.title;
    const _status = getStepStatus(_statusOrderStep, ORDER_STEPS.collected.id);
    let _description = '';

    if (_status === STEP_STATUS.finish) {
      let names = null;
      Object.entries(data?.collectors || {}).map(([id, value]) => {
        if (!names) {
          names = value.full_name;
        } else {
          names += ', ' + value.full_name;
        }
      });
      
      let images = [];
      let collectDate = undefined;
      Object.entries(data?.collect_proofs || {}).map(([id, value]) => {
        if (value?.images?.length > 0) {
          images.push(...value.images);
        }
        if (!collectDate) {
          collectDate = value?.created_at || 0;
        } else {
          collectDate = Math.max(collectDate, value?.created_at || 0);
        }
      });

      _description = (
        <div className="d-flex flex-column">
          <span>{collectDate ? moment(collectDate).format(DATA_FORMAT_WITH_TIME) : '-'}</span>
          <Row className="mx-0 my-1">
            {images.map((_img, id) => (
              <img 
                src={`${Config.BACKEND_API_URL}${_img}`}
                alt=""
                className="view-list-image"
                key={'collect-images-' + id}
              />
            ))}
          </Row>
          <span>Updated by {names || '-'}</span>
        </div>
      );
    } else {
      _description = '';
    }

    return {
      title: _title,
      description: _description,
      status: _status,
    };
  };

  const getPackedStepData = _statusOrderStep => {
    const _title = ORDER_STEPS.packed.title;
    const _status = getStepStatus(_statusOrderStep, ORDER_STEPS.packed.id);
    let _description = '';

    if (_status === STEP_STATUS.finish) {
      const name = data?.packer?.full_name || '-';
      
      const images = data?.packed_proof?.images || [];
      const packedDate = data?.packed_proof?.created_at || '';
      
      _description = (
        <div className="d-flex flex-column">
          <span>{packedDate ? moment(packedDate).format(DATA_FORMAT_WITH_TIME) : '-'}</span>
          <Row className="mx-0 my-1">
            {images.map((_img, id) => (
              <img 
                src={`${Config.BACKEND_API_URL}${_img}`}
                alt=""
                className="view-list-image"
                key={'packed-images-' + id}
              />
            ))}
          </Row>
          <span>Updated by {name || '-'}</span>
        </div>
      );
    } else {
      _description = '';
    }

    return {
      title: _title,
      description: _description,
      status: _status,
    };
  };

  const getOutDeliveryStepData = _statusOrderStep => {
    const _title = ORDER_STEPS.outDelivery.title;
    const _status = getStepStatus(_statusOrderStep, ORDER_STEPS.outDelivery.id);
    let _description = '';

    if (_status === STEP_STATUS.finish) {
      const outDeliveryDate = data?.out_for_delivery_at || '';
      const name = data?.deliver?.full_name || '-';
      const address = data?.delivery_address || '-';
      
      _description = (
        <div className="d-flex flex-column">
          <span>{outDeliveryDate ? moment(outDeliveryDate).format(DATA_FORMAT_WITH_TIME) : '-'}</span>
          <span>At {address || '-'}</span>
          <span>Updated by {name || '-'}</span>
        </div>
      );
    } else {
      _description = '';
    }

    return {
      title: _title,
      description: _description,
      status: _status,
    };
  };

  const getDeliveredStepData = _statusOrderStep => {
    const _title = ORDER_STEPS.delivered.title;
    const _status = getStepStatus(_statusOrderStep, ORDER_STEPS.delivered.id);
    console.log('_status: ', _status);
    let _description = '';

    if (_status === STEP_STATUS.finish) {
      const name = data?.deliver?.full_name || '-';
      const images = data?.delivered_proof?.images || [];
      const deliveredDate = data?.delivered_proof?.created_at || '';
      
      _description = (
        <div className="d-flex flex-column">
          <span>{deliveredDate ? moment(deliveredDate).format(DATA_FORMAT_WITH_TIME) : '-'}</span>
          <Row className="mx-0 my-1">
            {images.map((_img, id) => (
              <img 
                src={`${Config.BACKEND_API_URL}${_img}`}
                alt=""
                className="view-list-image"
                key={'packed-images-' + id}
              />
            ))}
          </Row>
          <span>Updated by {name || '-'}</span>
        </div>
      );
    } else {
      _description = '';
    }

    return {
      title: _title,
      description: _description,
      status: _status,
    };
  };

  const {
    stepsData,
    statusOrderStep,
   } = useMemo(() => {
    if (data) {
      const _statusOrderStep = ORDER_STATUS.findIndex(status => status === data?.status);
      console.log('_statusOrderStep: ', _statusOrderStep);
      console.log('data: ', data);
      const _stepsData = [];

      const pendingData = getPendingStepData(_statusOrderStep);
      _stepsData.push(pendingData);

      const collectedData = getColletedStepData(_statusOrderStep);
      _stepsData.push(collectedData);

      const packedData = getPackedStepData(_statusOrderStep);
      _stepsData.push(packedData);

      const outDeliveryData = getOutDeliveryStepData(_statusOrderStep);
      _stepsData.push(outDeliveryData);

      const deliveredData = getDeliveredStepData(_statusOrderStep);
      _stepsData.push(deliveredData);
      console.log('_stepsData: ', _stepsData);
  
      return {
        stepsData: _stepsData,
        statusOrderStep: _statusOrderStep,
      };
    }

    return {
      stepsData: [],
      statusOrderStep: -1,
    }
  }, [data]);

  const closeModal = () => handleOpen(false);

  const CommonStep = ({ stepData, ...restParam }) => { 
    return (
      <Steps.Step
        title={stepData?.title || ''} 
        description={stepData?.description || ''} 
        status={stepData?.status || STEP_STATUS.process}
        {...restParam}
      />
    )
  };

  return (
    <BaseModal
      open={open}
      handleOpen={_open => handleOpen(_open)}
      title="Order View"
      content={
        <Steps
          current={statusOrderStep + 1}
          direction="vertical"
          icons={
            {
              finish: <span className="finish-icon"></span>,
            }
          }
          status="process"
        >
          {stepsData?.map((_data, stepId) => {
            return (
              <CommonStep
                key={'step_' + stepId}
                stepData={_data}
              />
            );
          })}
        </Steps>
      }
      buttons={
        <>
          <Button color="primary" onClick={closeModal}>Close</Button>
        </>
      }
      {...restProps}
    />
  )
};

export default OrderView;


