import React, { Component, Fragment } from "react";
import { Row,Button,Card, CardBody, CardTitle,Table } from "reactstrap";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import APIServices from '../../../service/api/api'

export default class Issuers extends Component {
    constructor(props){
      super(props)
      this.state={
        currentPage:1,
        totalCategories:{},
        issuers:[],
        totalPage: 0,
        pageSize: 10
      }
    }
    componentDidMount(){
      this.refresh(this.state.currentPage);
    }
    refresh(currentPage) {
      const page= currentPage
      const size = this.state.pageSize
      APIServices.getIssuers(page,size,(err, res)=>{
        if (res&&res.success) {
          const issuers = res.data.data;
          this.setState({issuers, totalPage: Math.ceil(res.data.total / res.data.size)})
        } else {
        }
      })
    }

    setIssuerStatus(item) {
      var id= item.id
      if(!item.status||item.status!==1){
        APIServices.enRollIssuer(id,(err, res)=>{
          if (res&&res.success) {
            this.refresh(this.state.currentPage);
          } else {
          }
        })
      }
      else{
        APIServices.revokeIssuer(id,(err, res)=>{
          if (res&&res.success) {
            this.refresh(this.state.currentPage);
          } else {
          }
        })
      }
    }
    onChangePage(i){
      this.setState({currentPage:i})
      setImmediate(() => {
        const { currentPage } = this.state;
        this.refresh(currentPage);
      });
    }
    render() {
        const {issuers} = this.state
        return (
            <Fragment>
            <Row >
              <Colxx xxs="12">
                <Breadcrumb heading="Test centers" match={this.props.match} />
                <Separator className="mb-5" />
              </Colxx>
            </Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  {/* <CardTitle>
                    List of Issuers
                  </CardTitle> */}
                  <Table>
                    <thead>
                      <tr>
                        <th>Id</th>
                        <th>Email</th>
                        <th>Name</th>
                        <th>Status</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        (issuers&&issuers.length>0)&&issuers.map((item,index)=>{
                          return(
                            <tr key={index}>
                              <th style={{verticalAlign: 'middle'}} scope="row">{item.id}</th>
                              <td style={{verticalAlign: 'middle'}}>{item.email}</td>
                              <td style={{verticalAlign: 'middle'}}>{item.name}</td>
                              <td style={{verticalAlign: 'middle'}}>{item.status===1?"Activated":item.status===-1?'Deactivated':'Pending'}</td>
                              <td style={{verticalAlign: 'middle'}}>
                                <Button
                                  style={{width:'70px',height:'28px',padding:'0px'}}
                                  onClick={() => {this.setIssuerStatus(item)}}
                                  color={item.status===1?"primary":'secondary'}
                                  size="sm"
                                  className="top-right-button"
                                  >
                                  {item.status!==1?'Enroll':'Revoke'}
                                </Button>
                              </td>
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Colxx>
            <Pagination
              currentPage={this.state.currentPage}
              totalPage={this.state.totalPage}
              onChangePage={i => this.onChangePage(i)}
            />
          </Fragment>
        )
    }
}
