import React, { Component, Fragment } from "react";
import { Row,Button,Modal , ModalHeader,
  ModalBody,Card, CardBody, CardTitle,Table,
  ModalFooter, Label} from "reactstrap";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import CustomSelectInput from "../../../components/common/CustomSelectInput";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import AsyncSelect from 'react-select/async'
import Pagination from "../../../components/pages/Pagination";
import SellerProductService from '../../../service/graphql/SellerProduct'
import ProductService from '../../../service/graphql/Product'

export default class Products extends Component {
    constructor(props){
      super(props)
      this.state={
        modal:true,
        showDeleteModal:false,
        items:[
          {id:1,title:"Promotion A",type:"type",description:"description", category:'category'},
          {id:2,title:"Promotion B",description:"description",  category:'category'},
          {id:3,title:"Promotion C",description:"description",  category:'category'},
          {id:4,title:"Promotion D",description:"description",  category:'category'},
          {id:5,title:"Promotion E",description:"description",  category:'category'},
          // {id:6,title:"Promotion B", category:'category'},
          // {id:7,title:"Promotion B", category:'category'},
          // {id:8,title:"Promotion B", category:'category'},
          // {id:9,title:"Promotion B", category:'category'},
          // {id:10,title:"Promotion B", category:'category'},
        ],
        products:[],
        selectedPromotion:{},
        page: 1,
        pageSize: 20,
        totalPage: 0
      }
    }
    componentDidMount(){
      this.refresh(this.state.currentPage)
     }
     refresh(){
      SellerProductService.getSellerProducts({page:this.state.page, pageSize: this.state.pageSize}, response=>{
          if(response.status === 1){
            console.log("response.data.results----------->>>>",response.data.results)
            this.setState({
              products:response.data.results,
              page:response.data.page,
              pageSize: this.state.pageSize,
              totalPage: Math.ceil(response.data.total / response.data.pageSize)
            })
          }
      })
    }
    onChangePage(i){
      this.setState({page: i}, this.refresh)
    }
    toggleModal = () => {
      this.setState({
        modalOpen: !this.state.modalOpen
      });
    };
    toggle =()=>{
      this.setState({
        showDeleteModal: !this.state.showDeleteModal
      });
    }
    showDeleteModal(item,e){
      e.stopPropagation();
      e.preventDefault();

      this.setState({showDeleteModal:true,selectedItem:item})

    }
    deleteProductApply(){
      const {selectedItem} = this.state
      SellerProductService.deleteProduct(selectedItem._id,(error,res)=>{
        this.setState({currentPage:1})
        this.refresh(1); 
     })
    }
    handleChangeStart=(e)=>{
      const {selectedPromotion} = this.state
      selectedPromotion.startDateRange = e
      this.setState({selectedPromotion})
    }
    handleChangeEnd=(e)=>{
      const {selectedPromotion} = this.state
      selectedPromotion.endDateRange = e
      this.setState({selectedPromotion})
    }
       
    loadProductOptions(inputValue, callback){
      ProductService.getProducts(response=>{
        if(response.status === 1){
            const users = response.data.results
            console.log("users on load user.....", users)
            const tempOptions = []
            users.forEach(element => {
                const obj = {
                  label:element.product_title,
                  value:element._id,
                  id:element._id,
                }
                tempOptions.push(obj)
            });
            callback(tempOptions)
          }
      })
    };
    onChangeProduct(e){
      this.setState({selectedProductOption:e})
    }
    render() {
        return (
            <Fragment>
            <Row >
              <Colxx xxs="12">
                <Breadcrumb heading="Seller Products" match={this.props.match} />
                <Separator className="mb-5" />
              </Colxx>
            </Row>
            <Row className="display-flex flex-row-reverse mb-4 mr-4">
            </Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <div style={{display:'flex',flexDirection:'row',justifyContent:'space-between'}}>
                    <CardTitle>
                      List of Seller Products
                    </CardTitle>
                    {/* <div className="select-cover">
                      <AsyncSelect
                          components={{ Input: CustomSelectInput }}
                          className="react-select"
                          name="form-field-name"
                          loadOptions={this.loadProductOptions}
                          defaultOptions
                          value={this.state.selectedProductOption}
                          onChange={(e)=>{this.onChangeProduct(e)}}
                          // onInputChange={this.handleInputChange}
                      />
                    </div> */}
                  </div>
                  
                  <Table hover>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Title</th>
                        <th>Price</th>
                        <th>Seller User ID</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        this.state.products.map((item,index)=>{
                          return(
                            <tr
                            className="cursor-pointer"
                            onClick={()=>{this.props.history.push(`/app/sellerproducts/product-detail/${item._id}`);}}
                            >
                              <th scope="row">{item._id}</th>
                              <td>{item.seller_product_title}</td>
                              <td>{item.price}</td>
                              <td>{item.seller_user_id}</td>
                              <td>
                                <div className="delete-promotion">
                                <i className="simple-icon-pencil" onClick={()=>{this.props.history.push(`/app/sellerproducts/product-detail/${item._id}`);}}/> 
                                  <i className="simple-icon-trash ml-3" onClick={this.showDeleteModal.bind(this,item)}/> 
                                </div>
                              </td>
                             
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Colxx>
             <Modal isOpen={this.state.showDeleteModal} toggle={this.toggle}>
                    <ModalHeader toggle={this.toggle}>
                      Warning
                    </ModalHeader>
                    <ModalBody>
                          Are you sure to delete this product?
                    </ModalBody>
                    <ModalFooter>
                      <Button color="primary" onClick={()=>{this.toggle();this.deleteProductApply()}}>
                        Yes
                      </Button>{" "}
                      <Button color="secondary" onClick={this.toggle}>
                        Cancel
                      </Button>
                    </ModalFooter>
                  </Modal>
            <Pagination
              currentPage={this.state.page}
              totalPage={this.state.totalPage}
              onChangePage={i => this.onChangePage(i)}
            />
          </Fragment>
        )
    }
}
