import React, { Component, Fragment } from "react";
import { Row,Button,Modal , ModalHeader,
  ModalBody,
  Card, CardBody, CardTitle,
  ModalFooter, Table} from "reactstrap";
  
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import AddNewCity from "../../../components/modals/AddNewCity";
import CityService from '../../../service/graphql/City'
import moment from 'moment'
import { NotificationManager } from "../../../components/common/react-notifications";
// import ListPageHeading from '../../components/pages/ListPageHeading'

export default class Cities extends Component {
    constructor(props){
      super(props)
      this.state={
        modal:true,
        showDeleteModalFlag:false,
        selectedItem:{},
        selectedCity:{},
        cities:[],
        currentPage:1,
        pageSize:10
      }
    }
    componentDidMount(){
        this.refresh()
    }
    refresh(){
      const {currentPage,pageSize} = this.state
      CityService.getCities(currentPage,pageSize,"", response=>{
          if(response.status === 1){
            console.log("response.data----------->>>>",response.data)
            this.setState({
              cities:response.data.results,
              currentPage:response.data.page,
              totalPage: Math.ceil(response.data.total / response.data.pageSize)
            })
            // console.log("response.data===>",response.data)
          }
      })
    }
    onChangePage(i){
      this.setState({currentPage:i})
    }
    closeModal = () => {
      this.setState({
        modalOpen: false
      });
    };
    showModal = () => {
      this.setState({
        modalOpen: true
      });
    };
    toggle =()=>{
      this.setState({
        showDeleteModalFlag: false
      });
    }
    showDeleteModal(item,e){
      e.stopPropagation();
      e.preventDefault();
      this.setState({showDeleteModalFlag:true, selectedItem:item})
    }
    deleteCity(){
      const {selectedItem} = this.state
      console.log("selectedItem ..... on ----L>", selectedItem)
      CityService.deleteCity(selectedItem._id,(error,res)=>{
         this.refresh()
      })
    }
    onNewInstance(e){
        const {selectedItem} = this.state
        console.log("selected item on new xxinstance..... ", selectedItem)
        if(selectedItem.city_name === undefined || selectedItem.city_name.length === 0){
          NotificationManager.error(
            "Error message",
            "Please confirm valid data!",
            3000,
            () => {
              // alert("callback");
            },
            null,
            "error"
          );
          return;
        }

        CityService.postCity(this.state.selectedItem,(error,res)=>{
              this.closeModal()
              this.refresh()
        })
    }
    onChangeProperty(value,type){
      console.log('value on cities......',value)
      const {selectedItem} = this.state
      if(type==="image"){
        let reader = new FileReader();
        reader.readAsDataURL(value);
        reader.onloadend = () => {
          selectedItem[type] = value
          this.setState({selectedItem})          
        };
      }else{
        selectedItem[type] = value
        this.setState({selectedItem})
      }
      
    }
    render() {
        const {modalOpen,selectedCity,cities,selectedItem} = this.state
        return (
            <Fragment>
            <Row >
              <Colxx xxs="12">
                <Breadcrumb heading="Cities" match={this.props.match} />
                <Separator className="mb-5" />
              </Colxx>
            </Row>
            <Row className="display-flex flex-row-reverse mb-4 mr-4">
              <Button
                color="primary"
                size="lg"
                className="top-right-button"
                onClick={()=>{this.showModal();this.setState({selectedItem:{}})}}>
                Add New
              </Button>
            </Row>
            <Colxx xxs="12">
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    List of Cities
                  </CardTitle>
                  <Table hover>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Name</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        cities && cities.map((item,index)=>{
                          return(
                            <tr
                            key = {"operator_" + index}
                            style = {{cursor:'pointer'}}
                            onClick={()=>{this.setState({modalOpen:true, selectedItem:item})}}
                            >
                              <th scope="row">{item._id}</th>
                              <td>{item.city_name}</td>
                              <td>
                                <div className="delete-operator">
                                  <i className="simple-icon-pencil" onClick={()=>{this.setState({modalOpen:true, selectedItem:item})}}/> 
                                  <i className="simple-icon-trash ml-3" onClick={this.showDeleteModal.bind(this,item)}/> 
                                </div>
                              </td>
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Colxx>
             <Modal isOpen={this.state.showDeleteModalFlag} toggle={this.toggle}>
                    <ModalHeader toggle={this.toggle}>
                      Warning
                      {/* <IntlMessages id="modal.modal-title" /> */}
                    </ModalHeader>
                    <ModalBody>
                          Are you sure to delete this city?
                    </ModalBody>
                    <ModalFooter>
                      <Button color="primary" onClick={()=>{this.toggle();this.deleteCity()}}>
                        Yes
                      </Button>{" "}
                      <Button color="secondary" onClick={this.toggle}>
                        Cancel
                      </Button>
                    </ModalFooter>
                  </Modal>
            <AddNewCity
              selectedItem = {selectedItem}
              modalOpen = {modalOpen}
              closeModal={this.closeModal}
              onChangeProperty={this.onChangeProperty.bind(this)}
              selectedCity = {selectedCity}
              onSubmit={this.onNewInstance.bind(this)}
              // categories={categories}
            />
             <Pagination
              currentPage={this.state.currentPage}
              totalPage={this.state.totalPage}
              onChangePage={i => this.onChangePage(i)}
            />
          </Fragment>
        )
    }
}
