import React, { Component, Fragment } from "react";
import { Row,Button,Modal , ModalHeader,
  ModalBody,
  Card, CardBody, CardTitle,
  ModalFooter, Table} from "reactstrap";
  
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import AddNewHub from "../../../components/modals/AddNewHub";
import HubService from '../../../service/graphql/Hub'
import { NotificationManager } from "../../../components/common/react-notifications";


export default class Hubs extends Component {
    constructor(props){
      super(props)
      this.state={
        modal:true,
        showDeleteModalFlag:false,
        selectedItem:{},
        selectedHub:{},
        hubs:[],
        currentPage:1,
        pageSize: 10,
        totalPage: 0,        
      }
    }
    componentDidMount(){
        this.refresh()
    }
    refresh(){
      HubService.getHubs({pageSize: this.state.pageSize, page: this.state.currentPage}, response=>{
          if(response.status === 1){
            let tempHubs = response.data.results
            tempHubs.forEach(hub => {
              hub.hub_operator_id = hub?.hub_operator?._id
            });
            this.setState({
              hubs:tempHubs,
              currentPage:response.data.page,
              pageSize: response.data.pageSize,
              totalPage: Math.ceil(response.data.total / response.data.pageSize)
            })
          }
      })
    }
    onChangePage(i){
      this.setState({currentPage:i}, this.refresh)
    }
    closeModal = () => {
      this.setState({
        modalOpen: false
      });
    };
    showModal = () => {
      this.setState({
        modalOpen: true
      });
    };
    toggle =()=>{
      this.setState({
        showDeleteModalFlag: false
      });
    }
    showDeleteModal(item,e){
      e.stopPropagation();
      e.preventDefault();
      this.setState({showDeleteModalFlag:true, selectedItem:item})
    }
    deleteHub(){
      const {selectedItem} = this.state
      HubService.deleteHub(selectedItem._id,(error,res)=>{
         this.refresh()
      })
    }
    onNewInstance(e){
        const {selectedItem} = this.state
        console.log('params...',selectedItem)
        if(selectedItem.hub_identifier === undefined || selectedItem.hub_identifier.length === 0 ||
           selectedItem.hub_address === undefined || selectedItem.hub_address.length === 0 ||
           selectedItem.hub_operator_id === undefined || selectedItem.hub_operator_id === 0 
           ||(selectedItem.hub_radius === undefined||selectedItem.hub_radius === "")
           ){
          NotificationManager.error(
            "Error message",
            "Please confirm valid data!",
            3000,
            () => {
              // alert("callback");
            },
            null,
            "error"
          );
          return;
        }
        console.log('this.state.selectedItem', this.state.selectedItem);        
        HubService.postHub(this.state.selectedItem,(error,res)=>{
              console.log('error', res, error);
              return;
              this.closeModal()
              this.refresh()
        })
    }
    onChangeProperty(value,type){
      const {selectedItem} = this.state
      if(type==="image"){
        let reader = new FileReader();
        reader.readAsDataURL(value);
        reader.onloadend = () => {
          selectedItem[type] = value
          this.setState({selectedItem})          
        };
      }else{
        selectedItem[type] = value
        this.setState({selectedItem})
      }
      
    }
    setSelectItem(item){
      let {selectedItem} = this.state
      selectedItem=item
      selectedItem.hub_lon=item.location.coordinates[0]
      selectedItem.hub_lat=item.location.coordinates[1]
      this.setState({selectedItem})
      this.setState({modalOpen:true})
    }
    showHubTimes(item,e){
      e.stopPropagation();
      e.preventDefault();
      this.props.history.push(`/app/hubs/hub-timings/${item._id}`)
    }
    render() {
        const {modalOpen,selectedHub,hubs,selectedItem} = this.state
        return (
            <Fragment>
            <Row >
              <Colxx xxs="12">
                <Breadcrumb heading="Hubs" match={this.props.match} />
                <Separator className="mb-5" />
              </Colxx>
            </Row>
            <Row className="display-flex flex-row-reverse mb-4 mr-4">
              <Button
                color="primary"
                size="lg"
                className="top-right-button"
                onClick={()=>{this.showModal();this.setState({selectedItem:{}})}}>       
                Add New
              </Button>
            </Row>
            <Colxx xxs="12">
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    List of Hubs
                  </CardTitle>
                  <Table hover>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Identifier</th>
                        <th>Operator</th>
                        <th>Address</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        hubs && hubs.map((item,index)=>{
                          return(
                            <tr
                            key = {"operator_" + index}
                            style = {{cursor:'pointer'}}
                            // onClick={()=>{this.setState({modalOpen:true, selectedItem:item})}}
                            onClick={()=>{this.setSelectItem(item)}}>
                              <th scope="row">{item._id}</th>
                              <td>{item.hub_identifier}</td>
                              <td>{(item.hub_operator&&item.hub_operator.operator_partner_identifier)?item.hub_operator.operator_partner_identifier:''}</td>
                              <td>{item.hub_address}</td>
                              <td>
                                <div className="action-group">
                                  <i className="iconsminds-over-time-2" style={{marginRight:'5px'}} onClick={this.showHubTimes.bind(this,item)}/> 
                                  <i className="simple-icon-pencil mr-3 ml-2" onClick={()=>{this.setState({modalOpen:true, selectedItem:item})}}/> 
                                  <i className="simple-icon-trash" onClick={this.showDeleteModal.bind(this,item)}/> 
                                </div>
                              </td>
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Colxx>
             <Modal isOpen={this.state.showDeleteModalFlag} toggle={this.toggle}>
                <ModalHeader toggle={this.toggle}>
                  Warning
                  {/* <IntlMessages id="modal.modal-title" /> */}
                </ModalHeader>
                <ModalBody>
                      Are you sure to delete this hub?
                </ModalBody>
                <ModalFooter>
                  <Button color="primary" onClick={()=>{this.toggle();this.deleteHub()}}>
                    Yes
                  </Button>{" "}
                  <Button color="secondary" onClick={this.toggle}>
                    Cancel
                  </Button>
                </ModalFooter>
              </Modal>
            <AddNewHub
              selectedItem = {selectedItem}
              modalOpen = {modalOpen}
              closeModal={this.closeModal}
              onChangeProperty={this.onChangeProperty.bind(this)}
              selectedHub = {selectedHub}
              onSubmit={this.onNewInstance.bind(this)}
              // categories={categories}
            />
            <Pagination
              currentPage={this.state.currentPage}
              totalPage={this.state.totalPage}
              onChangePage={i => this.onChangePage(i)}
            />
          </Fragment>
        )
    }
}
