import React, { Component, Fragment } from "react";
import { Row,Button,Modal , ModalHeader,
  ModalBody,Card, CardBody, CardTitle,Table,CardSubtitle,
  ModalFooter,CustomInput } from "reactstrap";
import IntlMessages from "../../../helpers/IntlMessages";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import BrandService from '../../../service/graphql/Brand'
import { NotificationManager } from "../../../components/common/react-notifications";
import { NavLink } from "react-router-dom";
// import ListPageHeading from '../../components/pages/ListPageHeading'

export default class Brands extends Component {
    constructor(props){
      super(props)
      this.state={
        modal:true,
        showDeleteModal:false,
        items:[
          {id:1,name:"Meat",type:"type",description:"description", category:'category'},
          {id:2,name:"Deli",description:"description",  category:'category'},
          {id:3,name:"Seafood",description:"description",  category:'category'},
          {id:4,name:"Bakery",description:"description",  category:'category'},
          {id:5,name:"Pantry",description:"description",  category:'category'},
        ],
        selectedPromotion:{},
        selectedItem:{},
        categories:[],
        currentPage:1,
        brandName:'',
        totalCategories:{},
        brands:[],
        totalPage: 0,
        pageSize: 10,

      }
    }
    componentDidMount(){
      this.refresh(this.state.currentPage);
    }
    refresh(currentPage) {
      const params = {
        page: currentPage,
        pageSize: this.state.pageSize,
        langCode: "en",
      };
      BrandService.getBrands(params,response=>{
        if (response.status === 1) {
          const brands = response.data.results;
          this.setState({brands, totalPage: Math.ceil(response.data.total / response.data.pageSize)})
        } else {
        }
      })
    }
    onChangePage(i){
      this.setState({currentPage:i})
      setImmediate(() => {
        const { currentPage } = this.state;
        this.refresh(currentPage);
      });
    }
    toggle =()=>{
      this.setState({
        showDeleteModal: !this.state.showDeleteModal
      });
    }
    deleteBrand(){
      const {selectedItem} = this.state
      BrandService.deleteBrand(selectedItem._id,(error,res)=>{
        this.setState({currentPage:1})
        this.refresh(1); 
      })
    }
    onSubmit(e){
        const {selectedItem} = this.state
        const langDataTmp=[
          {
            lang_code: "en",
            title: "Clothing",
            description: ""
          },
          {
            lang_code: "zh",
            title: "衣服",
            description: ""        
          }
        ]
        if(selectedItem.prodcat_identifier === undefined ||selectedItem.prodcat_identifier ===''){
          NotificationManager.error(
            "Error message",
            "Please confirm valid data!",
            3000,
            () => {
            },
            null,
            "error"
          );
          return;
        }
        let langData = '['
        for (let i = 0; i < langDataTmp.length; i++) {
          langData += `{lang_code: "${langDataTmp[i].lang_code}",title: "${langDataTmp[i].title}",description: "${langDataTmp[i].description}"},`
        }
        langData += ']'
        const params={
          prodcatId: selectedItem._id?selectedItem._id:'', 
          prodcatParent: selectedItem.prodcat_parent?selectedItem.prodcat_parent:'', 
          prodcatActive: selectedItem.prodcat_active?true:false, 
          prodcatFeatured: selectedItem.prodcat_featured?true:false, 
          prodcatIdentifier:selectedItem.prodcat_identifier?selectedItem.prodcat_identifier:'',
          prodcatDisplayOrder: selectedItem.prodcat_display_order?selectedItem.prodcat_display_order:'',                        
          langData:langData
        }
        
        BrandService.postBrand(params,(error,res)=>{
          this.closeModal()
          this.setState({currentPage:1})
          this.refresh(1);
        })
    }
    onChangeProperty= (value,type) =>{
      const {selectedItem} = this.state
      if(type==='prodcat_active' || type==='prodcat_featured'){
        selectedItem[type]=!selectedItem[type]
      }
      else{
        selectedItem[type] = value
      }
      this.setState({selectedItem}) 
    }
    showDeleteModal(item,e){
      e.stopPropagation();
      e.preventDefault();
      this.setState({showDeleteModal:true,selectedItem:item})

    }
    handleCheckChange = (event, id) => {
      event.stopPropagation();
      event.preventDefault();
    }

    listRefresh(item,e){
      var categoriesTmp = [];
      var id=''
      const childIitem=item.childs?item.childs:{}
      const brandName=item.prodcat_identifier?item.prodcat_identifier:''
      for (id of Object.keys(childIitem)) {
        categoriesTmp.push(childIitem[id]);
      }
      this.setState({categories:categoriesTmp,brandName})
    }
    render() {
        const {brands,brandName} = this.state
        return (
            <Fragment>
            <Row >
              <Colxx xxs="12">
                <Breadcrumb heading="Brands" match={this.props.match} />
                <Separator className="mb-5" />
              </Colxx>
            </Row>
            <Row className="display-flex flex-row-reverse mb-4 mr-4">
              <NavLink to={{
                pathname:`/app/brands/brand_detail/0`,
                state: {selectedItem:{},categoryName:''}  
              }}>
                <div className="top-right-button-container">
                <Button
                  color="primary"
                  size="sm"
                  className="top-right-button"
                  >
                  <IntlMessages id="pages.add-new" />
                </Button>
                </div>
              </NavLink>
              
            </Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                     List of Brands
                  </CardTitle>
                  <Table>
                    <thead>
                      <tr>
                        <th>Id</th>
                        <th>Identifier</th>
                        <th>Tltle</th>
                        <th>Active</th>
                        <th>Featured</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        (brands&&brands.length>0)&&brands.map((item,index)=>{
                          return(
                            <tr key={index}>
                              <th scope="row">{item._id}</th>
                              <td>{item.brand_identifier}</td>
                              <td>{item.brand_title}</td>
                              <td>
                                <div className="custom-control custom-checkbox pl-1 align-self-center mr-4">
                                  <span>
                                    <img style={{width:'11px',height:'11px'}} alt="" src={item.brand_active?"/assets/img/check-symbol.png":'/assets/img/uncheck-symbol.png'} />
                                  </span>
                                </div>
                              </td>
                              <td>
                                <div className="custom-control custom-checkbox pl-1 align-self-center mr-4">
                                <span>
                                  <img style={{width:'11px',height:'11px'}} alt="" src={item.brand_featured==='true'?"/assets/img/check-symbol.png":'/assets/img/uncheck-symbol.png'} />
                                </span>
                                </div>
                              </td>
                              <td>
                                <div>
                                  <NavLink to={{
                                    pathname:`/app/brands/brand_detail/${item._id}`,
                                    state: {selectedItem:item,brandName:brandName}  
                                  }}>
                                    <i style = {{cursor:'pointer'}} className="simple-icon-pencil" 
                                    />
                                  </NavLink>
                                  <i style = {{marginLeft:'10px'}} className="simple-icon-trash" 
                                  onClick={this.showDeleteModal.bind(this,item)}
                                  /> 
                                </div>
                              </td>
                             
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Colxx>
             <Modal isOpen={this.state.showDeleteModal} toggle={this.toggle}>
              <ModalHeader toggle={this.toggle}>
                Warning
              </ModalHeader>
              <ModalBody>
                Are you sure to delete this brand?
              </ModalBody>
              <ModalFooter>
                <Button color="primary" onClick={()=>{this.toggle();this.deleteBrand()}}>
                  Yes
                </Button>
                <Button color="secondary" onClick={this.toggle}>
                  Cancel
                </Button>
              </ModalFooter>
            </Modal>
            <Pagination
              currentPage={this.state.currentPage}
              totalPage={this.state.totalPage}
              onChangePage={i => this.onChangePage(i)}
            />
          </Fragment>
        )
    }
}
