import gql from 'graphql-tag';
import GRAPHQL from './index'

export default class {
  
  
  static getLoginRecords(currentPage, pageSize, callback) {
    const query = {
      query: gql`
        {
          getLoginRecords(page: ${currentPage}, pageSize:${pageSize}) {
            status
            message
            success
            data
          }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        console.log("response on getLoginRecords --->" ,response)
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.getLoginRecords.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }
}
