import React, { useEffect, useState, Fragment, useMemo } from 'react';
import {
  Row,
  Card,
  CardBody,
  Label,
} from 'reactstrap';

import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';

import LogServices from '../../../service/graphql/Log';
import { API_TYPES, OCR_TYPES } from './constants';
var Loader = require('react-loader');

const OrderDetail = (props) => {
  const {
    match,
    match: {
      params: {
        id: logId
      }
    },
  } = props;

  const [logDetail, setLogDetail] = useState(null);
  const [loaded, setLoaded] = useState(true);

  useEffect(() => {
    if (logId !== '0') {
      const query = {
        id: logId,
      };
      setLoaded(false)
      LogServices.getApiLogDetail(query, res => {
        setLoaded(true)
        if (res.status === 1) {
          const _logDetail = res?.data || {};
          if (_logDetail.input) {
            _logDetail.input = JSON.parse(_logDetail.input);
            //_logDetail.input = _logDetail.input;
          }
          if (_logDetail.output) {
            _logDetail.output = _logDetail.output;
          }
          if (_logDetail.output.data) {
            _logDetail.output.data = _logDetail.output.data;
          }
          console.log('===========', _logDetail)
          setLogDetail(_logDetail);
        }
      });
    } else {
      setLogDetail(null);
    }
  }, [logId]);

  const getImageUrl = imgUrl => {
    if (imgUrl) {
      if (imgUrl.includes('data:image/')) {
        return imgUrl;
      } else {
        return `data:image/png;base64, ${imgUrl}`;
      }
    }

    return '';
  };

  const renderInput = () => {
    if (logDetail?.api === API_TYPES.livenessFaceCompare.value) {
      return (
        <>
          <Label className="mt-4">Liveness Type</Label>
          <h5>{logDetail?.input?.LivenessType || '-'}</h5>
          <Row className="w-100 d-flex justify-content-center mt-4">
            {logDetail?.input?.ImageBase64 && (
              // eslint-disable-next-line jsx-a11y/alt-text
              <img
                src={getImageUrl(logDetail?.input?.ImageBase64)}
                width="100%"
                style={{maxWidth: 300}}
              />
            )}
          </Row>
          <Row className="w-100 d-flex justify-content-center mt-4">
            <video
              src={`data:video/mp4;base64, ${logDetail?.input?.VideoBase64 || ''}`}
              autoPlay
              loop
              width={200}
            />
          </Row>
        </>
      );
    } else if (logDetail?.api === API_TYPES.ocrThaiApi.value) {
      return (
        <>
          <Label className="mt-4">Side</Label>
          <h5>{logDetail?.input?.Side || '-'}</h5>
          <Row className="w-100 d-flex justify-content-center mt-4">
            {logDetail?.input?.ImageBase64 && (
              // eslint-disable-next-line jsx-a11y/alt-text
              <img
                src={getImageUrl(logDetail?.input?.ImageBase64)}
                width="100%"
                style={{maxWidth: 300}}
              />
            )}
          </Row>
        </>
      );
    } else if (logDetail?.api === API_TYPES.mlidPassportOCR.value) {
      return (
        <>
          <Label className="mt-4">RetImage</Label>
          <h5>{logDetail?.input?.RetImage || '-'}</h5>
          <Row className="w-100 d-flex justify-content-center mt-4">
            {logDetail?.input?.ImageBase64 && (
              // eslint-disable-next-line jsx-a11y/alt-text
              <img
                src={getImageUrl(logDetail?.input?.ImageBase64)}
                width="100%"
                style={{maxWidth: 300}}
              />
            )}
          </Row>
        </>
      );
    }
  };

  const renderOutput = () => {
    if (logDetail?.api === API_TYPES.livenessFaceCompare.value) {
      return (
        <>
          <Label className="mt-4">Request ID</Label>
          <h5>{logDetail?.output?.data?.RequestId || '-'}</h5>

          <Label className="mt-4">Description</Label>
          <h5>{logDetail?.output?.data?.Description || '-'}</h5>
          
          <Label className="mt-4">Sim</Label>
          <h5>{logDetail?.output?.data?.Sim || '-'}</h5>
          
          <Label className="mt-4">Result</Label>
          <h5>{logDetail?.output?.data?.Result || '-'}</h5>
          
          <Row className="w-100 d-flex justify-content-center mt-4">
            {logDetail?.output?.data?.BestFrameBase64 && (
              // eslint-disable-next-line jsx-a11y/alt-text
              <img
                src={getImageUrl(logDetail?.output?.data?.BestFrameBase64)}
                width={200}
              />
            )}
          </Row>
        </>
      );
    } else if (logDetail?.api === API_TYPES.ocrThaiApi.value) {
      if (logDetail?.description === OCR_TYPES.tencentThaiOCR) {
        return (
          <>
            <Label className="mt-4">ID</Label>
            <h5>{logDetail?.output?.data?.ID || '-'}</h5>

            <Label className="mt-4">Request ID</Label>
            <h5>{logDetail?.output?.data?.RequestId || '-'}</h5>

            <Label className="mt-4">Full Name</Label>
            <h5>{logDetail?.output?.data?.EnFirstName || ''} {logDetail?.output?.data?.EnLastName || ''}</h5>

            <Label className="mt-4">Thai Name</Label>
            <h5>{logDetail?.output?.data?.ThaiName || '-'}</h5>

            <Label className="mt-4">Birthday</Label>
            <h5>{logDetail?.output?.data?.Birthday || '-'}</h5>
            
            <Label className="mt-4">Address</Label>
            <h5>{logDetail?.output?.data?.Address || '-'}</h5>

            <Label className="mt-4">Expiration Date</Label>
            <h5>{logDetail?.output?.data?.ExpirationDate || '-'}</h5>

            <Label className="mt-4">Issue Date</Label>
            <h5>{logDetail?.output?.data?.IssueDate || '-'}</h5>
            
            <Row className="w-100 d-flex justify-content-center mt-4">
              {logDetail?.output?.data?.BestFrameBase64 && (
                // eslint-disable-next-line jsx-a11y/alt-text
                <img
                  src={getImageUrl(logDetail?.output?.data?.BestFrameBase64)}
                  width={200}
                />
              )}
            </Row>
          </>
        );
      } else {
        const data = logDetail?.output?.data?.result;
        return (
          <>
            <Label className="mt-4">
              ID ( confidence: {data?.confidence?.id_number || '-'} )
            </Label>
            <h5>{data?.id_number || '-'}</h5>

            <Label className="mt-4">Full Name</Label>
            <h5>{data?.first_name_en || ''} {data?.last_name_en || ''}</h5>

            <Label className="mt-4">
              Thai Name ( confidence: {data?.confidence?.name_th || '-'} )
            </Label>
            <h5>{data?.name_th || '-'}</h5>

            <Label className="mt-4">
              Birthday ( confidence: {data?.confidence?.date_of_birth_en || '-'} )
            </Label>
            <h5>{data?.date_of_birth_en || '-'}</h5>

            <Label className="mt-4">
              Address ( confidence: {data?.confidence?.address_th || '-'} )
            </Label>
            <h5>{data?.address_th || '-'}</h5>

            <Label className="mt-4">
              Religion ( confidence: {data?.confidence?.religion_th || '-'} )
            </Label>
            <h5>{data?.religion_th || '-'}</h5>

            <Label className="mt-4">
              Expiration Date ( confidence: {data?.confidence?.date_of_expiry_th || '-'} )
            </Label>
            <h5>{data?.date_of_expiry_th || '-'}</h5>

            <Label className="mt-4">
              Issue Date ( confidence: {data?.confidence?.date_of_issue_th || '-'} )
            </Label>
            <h5>{data?.date_of_issue_th || '-'}</h5>

            <Label className="mt-4">
              Serial Number ( confidence: {data?.confidence?.serial_number || '-'} )
            </Label>
            <h5>{data?.serial_number || '-'}</h5>

            <Label className="mt-4">Side</Label>
            <h5>{data?.side || '-'}</h5>
          </>
        );
      }
    } else if (logDetail?.api === API_TYPES.mlidPassportOCR.value) {
      const data = logDetail?.output?.data;
      const advancedData = data?.AdvancedInfo ? JSON.parse(data?.AdvancedInfo) : null;

      return (
        <>
          <Label className="mt-4">
            ID ( confidence: {advancedData?.ID?.Confidence || '-'} )
          </Label>
          <h5>{data?.ID || '-'}</h5>

          <Label className="mt-4">Request ID</Label>
          <h5>{data?.RequestId || '-'}</h5>

          <Label className="mt-4">Name</Label>
          <h5>{data?.Name || '-'}</h5>
          
          <Label className="mt-4">
            Birthday ( confidence: {advancedData?.DateOfBirth?.Confidence || '-'} )
          </Label>
          <h5>{data?.DateOfBirth || '-'}</h5>
          
          <Label className="mt-4">
            Gender ( confidence: {advancedData?.Sex?.Confidence || '-'} )
          </Label>
          <h5>{data?.Sex || '-'}</h5>

          <Label className="mt-4">
            Nationality ( confidence: {advancedData?.Nationality?.Confidence || '-'} )
          </Label>
          <h5>{data?.Nationality || '-'}</h5>

          <Label className="mt-4">
            Expiration Date ( confidence: {advancedData?.DateOfExpiration?.Confidence || '-'} )
          </Label>
          <h5>{data?.DateOfExpiration || '-'}</h5>

          <Label className="mt-4">Issue Country</Label>
          <h5>{data?.IssuingCountry || '-'}</h5>
          
          <Row className="w-100 d-flex justify-content-center mt-4">
            {data?.Image && (
              // eslint-disable-next-line jsx-a11y/alt-text
              <img
                src={getImageUrl(data?.Image)}
                width={200}
              />
            )}
          </Row>
        </>
      );
    }
  };

  return (
    <Fragment>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb
            heading={logId === "0" ? 'New Log' : 'Log Detail'}
            match={match}
          />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Loader loaded={loaded}>
      <Row className="mb-4">
        <Colxx xxs="12" md="12">
          <Card>
            <CardBody className="d-flex flex-column">
              <Row>
                <div className="flex-1">
                  <Label>API Name</Label>
                  <h5>{logDetail?.api || '-'}</h5>
                  <Label className="mt-4">Description</Label>
                  <h5>{logDetail?.description || '-'}</h5>

                  <h2 className="mt-5 w-100">Input</h2>
                  {renderInput()}
                </div>
                <div className="flex-1">
                  <Label className="mt-4">Created At</Label>
                  <h5>{logDetail?._created || '-'}</h5>

                  <h2 className="w-100" style={{marginTop: 100}}>Output</h2>
                  {renderOutput()}
                </div>
              </Row>
            </CardBody>
          </Card>
        </Colxx>
      </Row>
      </Loader>
    </Fragment>
  );
};

export default OrderDetail;
