import React, { useEffect, Fragment, useMemo, useState } from 'react';
import {
  Row,
  Label,
  Card,
  CardBody,
  CardTitle,
  Button,
  CardText
} from 'reactstrap';
import AsyncSelect from 'react-select/async';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import CustomSelectInput from "../../../components/common/CustomSelectInput";
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import Pagination from '../../../components/pages/Pagination';
import LogServices from '../../../service/graphql/Log';
import { BaseTable, BaseModal } from '../common';
import {
  LOG_TABLE_ITEMS,
  API_TYPES,
  PAGE_SIZE,
  FILTER_RESULT
} from './constants';
import './style.css';
var Loader = require('react-loader');

const LogList = props => {

  const { match, history } = props;

  const pageSize = PAGE_SIZE;

  const [apiType, setApiType] = useState({ key: 'LivenessFaceCompare', ...API_TYPES['LivenessFaceCompare'] });
  const [filterResult, setFilterResult] = useState({ key: 'all', ...FILTER_RESULT['all'] });
  
  const [logs, setLogs] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPage, setTotalPage] = useState(1);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [selectedLog, setSelectedLog] = useState(null);
  const [loaded, setLoaded] = useState(true);
  const [successCount, setSuccessCount] = useState(0);
  const [failureCount, setFailureCount] = useState(0);

  const openDeleteModal = () => setShowDeleteModal(true);
  const closeDeleteModal = () => setShowDeleteModal(false);

  const onChangePage = i => {
    setCurrentPage(i);
  };

  const loadFilterOptions = (inputValue, callback) => {
    const apiList = Object.getOwnPropertyNames(FILTER_RESULT).map(key => {
        return {
        ...FILTER_RESULT[key],
        key
      };
    });
    callback(apiList.filter(item => item.label.toLowerCase().includes(inputValue.toLowerCase())));
  };

  const getLogs = () => {
    const query = {
      page: currentPage,
      pageSize: pageSize,
      keyword: 'LivenessFaceCompare',
      result:filterResult.value
    };
    setLoaded(false)
    LogServices.getApiLogs(query, res => {
      setLoaded(true)
      console.log('res', res);
      if (res.status === 1) {
        const numPages = Math.ceil((res?.data?.total || 0) / pageSize);
        const _logs = res?.data?.logs?.map(item => {
          const output = item.output || '{}';
          // const output = JSON.parse(item.output || '{}');
          return {
            ...item,
            output: {
              ...output,
              // data: JSON.parse(output.data || '{}')
              data: output.data || '{}'
            },
          };
        });
        const successCount = res?.data?.success_count||0
        const failureCount = res?.data?.failed_count||0
        setSuccessCount(successCount)
        setFailureCount(failureCount)
        setLogs(_logs || []);
        setTotalPage(numPages|| 1);
      }
    });
  };

  useEffect(() => {
    getLogs();
  }, [pageSize, currentPage, filterResult]);

  const logTableItems = useMemo(() => {
    const _logTableItems = LOG_TABLE_ITEMS.filter(item => {
      if (item.apiTypes === undefined) {
        return true;
      } else {
        const enabled = item.apiTypes.find(tp => tp === apiType.key);
        return !!enabled;
      }
    });
    return _logTableItems;
  }, [apiType]);

  const handleDeleteLog = item => {
    setSelectedLog(item);
    openDeleteModal();
  };

  const deleteLog = () => {
    const query = {
      id: selectedLog._id,
    };

    LogServices.deleteApiLog(query, res => {
      if (res.status === 1) {
        getLogs();
      }
    });

    closeDeleteModal();
  }

  return (
    <Fragment>
        <Row>
          <Colxx xxs="12">
            <Breadcrumb heading="Logs" match={match} />
            <Separator className="mb-5" />
          </Colxx>
        </Row>
        <Loader loaded={loaded}>
        <Row className="row mx-3 mb-3 display-flex flex-row align-items-center">
          <Label className="mb-0 mr-3">
            Filter Result
          </Label>
          <AsyncSelect
            required
            components={{ Input: CustomSelectInput }}
            className="react-select api-types"
            name="form-field-name"
            loadOptions={loadFilterOptions}
            defaultOptions
            value={filterResult}
            onChange={e => setFilterResult(e)}
          />
          <Label className="mb-0 ml-3">
            Success
          </Label>
          <CardText className="mb-0 ml-3">
            {successCount}
          </CardText>
          <Label className="mb-0 ml-3">
            Failure
          </Label>
          <CardText className="mb-0 ml-3">
            {failureCount}
          </CardText>
        </Row>
        <Colxx>
          <Card>
            <CardBody>
              <CardTitle>List of Logs</CardTitle>
              <BaseTable
                title="List of Logs"
                data={logs}
                columns={logTableItems}
                actions={(item, index) => {
                  return (
                    <div>
                      <i
                        className="simple-icon-eye"
                        style={{ cursor:'pointer' }}
                        onClick={() => history.push(`/app/logs/log-detail/${item._id}`)}
                      />
                      <i
                        className="simple-icon-trash"
                        style = {{ marginLeft: '10px', cursor:'pointer' }}
                        onClick={() => handleDeleteLog(item)}
                      />
                    </div>
                  );
                }}
              />
            </CardBody>
          </Card>
        </Colxx>
        <Pagination
          currentPage={currentPage}
          totalPage={totalPage}
          onChangePage={(i) => onChangePage(i)}
        />
        <BaseModal
          open={showDeleteModal}
          handleOpen={open => setShowDeleteModal(open)}
          title="Warning"
          content="Are you sure to delete this log?"
          buttons={
            <>
              <Button color="primary" onClick={deleteLog}>Yes</Button>
              <Button color="secondary" onClick={closeDeleteModal}>Cancel</Button>
            </>
          }
        />
      </Loader>
    </Fragment>
  );
};

export default LogList;
