import React, { Component, Fragment } from "react";
import IntlMessages from "../../../helpers/IntlMessages";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import {
  Row,
  Card,
  CardBody,
  Input,
  CardTitle,
  Button,
  Label,
  Form,
  CustomInput
} from "reactstrap";
import "react-tagsinput/react-tagsinput.css";
import "react-datepicker/dist/react-datepicker.css";
import "rc-switch/assets/index.css";
import "rc-slider/assets/index.css";
import "react-rater/lib/react-rater.css";
import Switch from "rc-switch";
import AsyncSelect from 'react-select/async'
import BrandService from '../../../service/graphql/Brand'
import CustomSelectInput from "../../../components/common/CustomSelectInput";
import { NotificationManager } from "../../../components/common/react-notifications";
import LanguageSelector from '../../../components/LanguageSelector'
import {plainListToGql,gqlToLanguageOptions,getDefaultLanguageOptions} from '../../../libs/converter'


export default class BrandDetail extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedOption: "",
      selectedItem:this.props.location.state.selectedItem?this.props.location.state.selectedItem:{},
      // langs:[],
      langs:(this.props.location.state.selectedItem&&this.props.location.state.selectedItem.langs)?this.props.location.state.selectedItem.langs:[],
      selectedItemActive:this.props.location.state.selectedItem?this.props.location.state.selectedItem.brand_active:false,
      selectedItemFeatured:(this.props.location.state.selectedItem&&this.props.location.state.selectedItem.brand_featured=='true')?true:false,
      langOptions:[],
      selectedLangIndex:0
    };
  }
    componentDidMount(){
      if(this.state.selectedItem.langs){
        this.langOptionsInit() 
      }
      else{
        this._getLangOptions()  
      }
    }
    langOptionsInit(){
      const {langs}= this.state
      var langOptionsTmp=[];
      if(langs&&langs.length>0){
        this.setState({langOptions:gqlToLanguageOptions(langs,"brand_label")})
      }   
    }
    _getLangOptions(){
      getDefaultLanguageOptions((options)=>{
        this.setState({langOptions:options})
      })
    }
    onChangeProperty(value,type){
      const {selectedItem} = this.state
      if(type==='brand_active'){
        selectedItem[type]=value
        this.setState({selectedItemActive:value})
      }
      else if(type==='brand_featured'){
        selectedItem[type]=value?'true':'false'
        this.setState({selectedItemFeatured:value})
      }
      else{
        selectedItem[type] = value
      }
      this.setState({selectedItem}) 
    }
    onSubmit(e){
      const {selectedItem,langOptions} = this.state
      if(selectedItem.brand_identifier === undefined ||selectedItem.brand_identifier ===''){
        NotificationManager.error(
          "Error message",
          "Please confirm valid data!",
          3000,
          () => {
          },
          null,
          "error"
        );
        return;
      }
      const langData= plainListToGql(langOptions)
      const params={
        brandId: selectedItem._id?selectedItem._id:'',
        brandActive: selectedItem.brand_active?true:false, 
        brandFeatured: selectedItem.brand_featured==='true'?true:false,
        brandIdentifier:selectedItem.brand_identifier?selectedItem.brand_identifier:'',
        brandSellerId: selectedItem.brand_seller_id?selectedItem.brand_seller_id:'',                        
        langData:langData
      }
      BrandService.postBrand(params,(error,res)=>{
        this.props.history.push(`/app/brands/list`)
      })
    }
    onclose(e){
      this.props.history.push(`/app/brands/list`)
    }
  
  _getCategories(){
    var responseCategories = [];
    var id=''
    const params = {
      langCode: "en",
    };
    BrandService.getBrands(params,response=>{
      if (response.status === 1) {
        const brands = response.data;
        for (id of Object.keys(brands)) {
          responseCategories.push(brands[id]);
        }
      } else {
      }
      this.setState({brands:responseCategories})
    })
    }
    onChangeName(value){
      const {selectedLangIndex} = this.state
      const selectedLangOption = this.state.langOptions[selectedLangIndex];
      selectedLangOption.title = value
      this.setState({langOptions:[...this.state.langOptions]})
    }
    
    render() {
      const {selectedItem,brandName,selectedItemActive,selectedItemFeatured} = this.state
        return (
          <Fragment>
            <Row>
              <Colxx xxs="12">
                <Breadcrumb heading={selectedItem._id?"Edit Brand":"Add New Brand"} match={this.props.match} />
                <Separator className="mb-5" />
              </Colxx>
            </Row>
            <Row className="mb-4">
            <Colxx xxs="12" md="12">
              <Card>
                <CardBody>
                  <CardTitle>
                    Input Details
                  </CardTitle>

                  <Form>
                    <Label>
                      Identifier
                    </Label>
                    <Input onChange={(e)=>{this.onChangeProperty(e.target.value,'brand_identifier')}} value={selectedItem.brand_identifier}/>
                    <LanguageSelector 
                      langOptions={this.state.langOptions} 
                      onChangeName={this.onChangeName.bind(this)}
                      selectedLangIndex = {this.state.selectedLangIndex}
                      onChangeTab = {(index)=>{this.setState({selectedLangIndex:index})}}
                    />
                    <Label>
                      Active
                    </Label>
                    <Switch
                      className="custom-switch custom-switch-primary custom-switch-small"
                      checked={selectedItemActive}
                      onChange={value=>{this.onChangeProperty(value, 'brand_active')}}
                    />
                    {/* <CustomInput
                      className="itemCheck mb-0"
                      type="checkbox"
                      id={`check_${selectedItem.brand_identifier}`}
                      checked={selectedItem.brand_active?true:false}
                      // onChange={(e)=>{this.onChangeProperty(e.target.value, 'brand_active')}}
                      onChange={event => this.onChangeProperty(event, 'brand_active')}
                      label=""
                    /> */}
                    <Label className="mt-4">
                      Featured
                    </Label>
                    <Switch
                      className="custom-switch custom-switch-primary custom-switch-small"
                      checked={selectedItemFeatured}
                      onChange={value=>{this.onChangeProperty(value, 'brand_featured')}}
                    />
                    {/* <CustomInput
                      className="itemCheck mb-0"
                      type="checkbox"
                      id={`check_${selectedItem.brand_identifier}`}
                      checked={selectedItem.brand_featured=='true'?true:false}
                      // onChange={(e)=>{onChangeProperty(e.target.value, 'brand_active')}}
                      onChange={event => this.onChangeProperty(event, 'brand_featured')}
                      label=""
                    /> */}
                    <Button color="secondary" className="mt-4" outline
                    onClick={()=>{
                      this.onclose()}}>
                      <IntlMessages id="forms.cancel" />
                    </Button>
                    <Button style={{marginLeft:'15px'}} color="primary" className="mt-4" onClick={()=>{
                      this.onSubmit()
                    }}>
                      <IntlMessages id="forms.submit" />
                    </Button>
                  </Form>
                </CardBody>
              </Card>
            </Colxx>
          </Row>
          </Fragment>
        )
    }
}
