import React,{useState} from "react";
import {
  CustomInput,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Form
  
} from "reactstrap";
import "react-datepicker/dist/react-datepicker.css";
import IntlMessages from "../../helpers/IntlMessages";

const AddNewModal = ({ modalOpen,selectedItem, toggleModal,onSubmit,closeModal,onChangeProperty,parent }) => {
  const [enablePasswordChange, setEnablePasswordChange] = useState(false)
  const updateCheckBox = (field)=>{
     selectedItem[field] = !selectedItem[field]
     parent.setState({selectedItem})
  }
  const handleSubmit = (e)=>{
    if(!enablePasswordChange){
      selectedItem.user_password=""
    }
    e.stopPropagation();
    e.preventDefault();
    onSubmit();
  }
  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
          {selectedItem._id?"Edit User":"Add New User"}
      </ModalHeader> 
      <Form onSubmit={handleSubmit}>
      <ModalBody>
        <CustomInput
            className="mt-2"
            type="checkbox"
            id="box1"
            label="Is Admin?"
            checked={selectedItem.is_admin}
            onChange = {(e)=>{updateCheckBox("is_admin")}}
        />
        <CustomInput
            className="mt-2"
            type="checkbox"
            id="box2"
            label="Is Collector?"
            checked={selectedItem.is_collector}
            onChange = {(e)=>{updateCheckBox("is_collector")}}
        />
        <CustomInput
            className="mt-2"
            type="checkbox"
            id="box3"
            label="Is Deliver?"
            checked={selectedItem.is_deliver}
            onChange = {(e)=>{updateCheckBox("is_deliver")}}
        />
        <CustomInput
            className="mt-2 mb-2"
            type="checkbox"
            id="box4"
            label="Is Sorter?"
            checked={selectedItem.is_sorter}
            onChange = {(e)=>{updateCheckBox("is_sorter")}}
        />
        <Label className="mt-4">
            Full Name
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'full_name')}} value={selectedItem.full_name}/>
        <Label className="mt-4">
            Wallet Address
        </Label>
        <Input  required  onChange={(e)=>{onChangeProperty(e.target.value,'wallet_address')}} value={selectedItem.wallet_address}/>
        <Label className="mt-4">
            Email
        </Label>
        <Input  required onChange={(e)=>{onChangeProperty(e.target.value,'email')}} value={selectedItem.email}/>
        <CustomInput
            className="mt-4"
            type="checkbox"
            id="box5"
            label="Are you going to change password?"
            checked={enablePasswordChange}
            onChange = {(e)=>{setEnablePasswordChange(!enablePasswordChange)}}
        />
        <Label className="mt-4">
            Password
        </Label>
        <Input type="password" disabled={!enablePasswordChange} onChange={(e)=>{onChangeProperty(e.target.value,'user_password')}} value={selectedItem.user_password}/>
      </ModalBody>
      
      <ModalFooter>
        <Button color="secondary" outline onClick={closeModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button color="primary" type="submit">
          <IntlMessages id="pages.submit" />
        </Button>{" "}
      </ModalFooter>
      </Form>
    </Modal>
  );
};

export default AddNewModal;
