import gql from 'graphql-tag';
import GRAPHQL from './index'
import moment from 'moment'

export default class {
  
  
  static getOperators(data, callback) {
    const query = {
      query: gql`
        {
          saleGetShopOperators(page:${data.page},pageSize:${data.pageSize}){
          status
          message
          success
          data
        }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetShopOperators.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static postOperator(data,callback) {
    const query = {
      mutation: gql`
          mutation{
            salePostShopOperator(
              operator_id:"${data._id?data._id:''}",
              operator_partner_identifier: "${data.operator_partner_identifier}", 
              api_endpoint:"${data.api_endpoint}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }
    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostShopOperator.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static deleteOperator(id,callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteShopOperator(
              operator_id:"${id}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.getOperators.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

 
}
