import React, { Component, Fragment } from "react";
import {
  Row,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  Card,
  CardBody,
  CardTitle,
  ModalFooter,
  Table,
} from "reactstrap";

import IntlMessages from "../../../helpers/IntlMessages";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Pagination from "../../../components/pages/Pagination";
import AddNewModal from "../../../components/pages/AddNewModal";
import PromotionService from "../../../service/graphql/Promotion";

import moment from "moment";
import { NotificationManager } from "../../../components/common/react-notifications";
import Config from '../../../service/api/config'
// import ListPageHeading from '../../components/pages/ListPageHeading'

export default class Promotions extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: true,
      showDeleteModalFlag: false,
      selectedItem: {
        start_date:Math.floor(Date.now()/1000),
        end_date:Math.floor(Date.now()/1000)
      },
      selectedPromotion: {},
      promotions: [],
      currentPage: 1,
      
    };
  }
  componentDidMount() {
    this.refresh();
  }
  refresh() {
    PromotionService.getPromotions((response) => {
      if (response.status === 1) {
        this.setState({ promotions: response.data.data });
        console.log("response.data=on refresh...==>", response.data.data);
      }
    });
  }
  onChangePage(i) {
    this.setState({ currentPage: i });
  }
  closeModal = () => {
    this.setState({
      modalOpen: false,
    });
  };
  showModal = () => {
    this.setState({
      modalOpen: true,
    });
  };
  toggle = () => {
    this.setState({
      showDeleteModalFlag: false,
    });
  };
  showDeleteModal(item, e) {
    e.stopPropagation();
    e.preventDefault();
    this.setState({ showDeleteModalFlag: true, selectedItem: item });
  }
  deletePromotion() {
    const { selectedItem } = this.state;
    console.log("selectedItem ..... on ----L>", selectedItem);
    PromotionService.deletePromotion(selectedItem._id, (error, res) => {
      this.refresh();
    });
  }
  onNewInstance(e) {
    const { selectedItem } = this.state;
    // if (
    //   selectedItem.name === undefined ||
    //   selectedItem.name.length === 0 ||
    //   selectedItem.discount_type === undefined ||
    //   selectedItem.discount_type.length === 0 ||
    //   selectedItem.discount_value === undefined ||
    //   selectedItem.discount_value.length === 0 ||
    //   selectedItem.promotion_type === undefined ||
    //   selectedItem.promotion_type.length === 0
    // ) {
    //   NotificationManager.error(
    //     "Error message",
    //     "Please confirm valid data!",
    //     3000,
    //     () => {
    //       // alert("callback");
    //     },
    //     null,
    //     "error"
    //   );
    //   return;
    // }
    console.log("promotion is being created.....")
    PromotionService.postPromotion(this.state.selectedItem, (error, res) => {
      console.log("postPromotion res----->>>", error,"res...",res);
      this.closeModal();
      setTimeout(()=>{
        this.refresh();
      },1000)
      
    });
  }
  onChangeProperty(value, type) {
    const { selectedItem } = this.state;
    if (type === "start_date" || type === "end_date") {
      selectedItem[type] = value / 1000;
      this.setState({ selectedItem });
    } else if (type === "image") {
      let reader = new FileReader();
      reader.readAsDataURL(value);
      reader.onloadend = () => {
        selectedItem[type] = reader.result;
        this.setState({ selectedItem });
      };
    } else {
      selectedItem[type] = value;
      this.setState({ selectedItem });
    }
  }
  render() {
    const {
      modalOpen,
      selectedPromotion,
      promotions,
      selectedItem,
    } = this.state;
    let suffix = moment()
    return (
      <Fragment>
        <Row>
          <Colxx xxs="12">
            <Breadcrumb heading="Promotions" match={this.props.match} />
            <Separator className="mb-5" />
          </Colxx>
        </Row>
        <Row className="display-flex flex-row-reverse mb-4 mr-4">
          <Button
            color="primary"
            size="lg"
            className="top-right-button"
            onClick={() => {
              this.showModal();
              this.setState({ selectedItem: {
                start_date:Math.floor(Date.now()/1000),
                end_date:Math.floor(Date.now()/1000)
              } });
            }}
          >
            Add New
          </Button>
        </Row>
        <Colxx xxs="12">
          <Card className="mb-4">
            <CardBody>
          <CardTitle>List of Promotion:{promotions.length}</CardTitle>
              <Table hover>
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Image</th>
                    <th>Name</th>
                    <th>Start Date</th>
                    <th>End Date</th>
                    <th>Discount Type</th>
                    <th>Discount Value</th>
                    <th>Promotion Type</th>
                    <th>Enabled</th>
                    <th>Special Code</th>
                    <th >Action</th>
                  </tr>
                </thead>
                <tbody>
                  {!!promotions && promotions.length>0 && promotions.map((item, index) => {
                    return (
                      <tr
                        key={"promotion_" + index}
                        style={{ cursor: "pointer" }}
                        onClick={() => {
                          item.image += `?time=${suffix}`
                          this.setState({
                            modalOpen: true,
                            selectedItem: item,
                          });
                        }}
                      >
                        <th scope="row">{item._id}</th>
                        <td>
                          <img
                            className="thumbnail-image"
                            src={Config.BACKEND_API_URL+ item.image + `?time=${suffix}`}
                          />
                        </td>
                        <td>{item.name}</td>
                        <td>
                          {moment(item.start_date * 1000).format("YYYY/MM/DD")}
                        </td>
                        <td>
                          {moment(item.end_date * 1000).format("YYYY/MM/DD")}
                        </td>
                        <td>{item.discount_type}</td>
                        <td>{item.discount_value}</td>
                        <td>{item.promotion_type}</td>
                        <td>{item.enable_status ? "True" : "False"}</td>
                        <td>{item.special_code}</td>
                        <td >
                          <div className="action-group">
                            <i
                              className="simple-icon-pencil"
                              onClick={() => {
                                item.image += `?time=${suffix}`
                                this.setState({
                                  modalOpen: true,
                                  selectedItem: item,
                                });
                              }}
                            />
                            <i style={{marginLeft:'10px'}}
                              className="simple-icon-trash"
                              onClick={this.showDeleteModal.bind(this, item)}
                            />
                          </div>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Colxx>
        <Modal isOpen={this.state.showDeleteModalFlag} toggle={this.toggle}>
          <ModalHeader toggle={this.toggle}>
            Warning
            {/* <IntlMessages id="modal.modal-title" /> */}
          </ModalHeader>
          <ModalBody>Are you sure to delete this promotion?</ModalBody>
          <ModalFooter>
            <Button
              color="primary"
              onClick={() => {
                this.toggle();
                this.deletePromotion();
              }}
            >
              Yes
            </Button>{" "}
            <Button color="secondary" onClick={this.toggle}>
              Cancel
            </Button>
          </ModalFooter>
        </Modal>
        <AddNewModal
          selectedItem={selectedItem}
          modalOpen={modalOpen}
          closeModal={this.closeModal}
          onChangeProperty={this.onChangeProperty.bind(this)}
          selectedPromotion={selectedPromotion}
          onSubmit={this.onNewInstance.bind(this)}
          // categories={categories}
        />
        {/* <Pagination
          currentPage={this.state.currentPage}
          totalPage={10}
          onChangePage={(i) => this.onChangePage(i)}
        /> */}
      </Fragment>
    );
  }
}
