import React,{useEffect,useState} from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Form
} from "reactstrap";
import "react-datepicker/dist/react-datepicker.css";
import IntlMessages from "../../helpers/IntlMessages";
import AsyncSelect from 'react-select/async'
import LanguageSelector from '../LanguageSelector'
import {plainListToGql,gqlToLanguageOptions,getDefaultLanguageOptions} from '../../libs/converter'
import CountryService from '../../service/graphql/Country'
import CustomSelectInput from "../../components/common/CustomSelectInput";


const AddNewModal = ({ modalOpen,selectedItem, toggleModal,onSubmit,closeModal,onChangeProperty }) => {
  const [langOptions,setLangOptions] = useState([])
  const [countryOptions,setCountryOptions] = useState([])
  const [selectedLangIndex,setSelectedLangIndex] = useState(0)
  const getCountries = ()=>{
    CountryService.getCountryOptions(response=>{
        if(response.status === 1){
          const operators = response.data.results
          const tempOptions = []
          operators.forEach(element => {
              const obj = {
                label:element.country_code,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          setCountryOptions(tempOptions)
        }
    })
  }
  useEffect(()=>{
      getDefaultLanguageOptions((options)=>{
        if(selectedItem.langs){
          setLangOptions(gqlToLanguageOptions(selectedItem.langs,"state_label"))
        }else{
          setLangOptions(options)
          selectedItem.lang_data = plainListToGql(options)
        }
        // setImmediate(()=>{
        //   selectedItem.lang_data = plainListToGql(langOptions)
        // })
      })
      // CountryService()
      getCountries()
     
      
  },[selectedItem.langs,modalOpen])
  
  const onChangeName = (value)=>{
    const selectedLangOption = langOptions[selectedLangIndex];
    selectedLangOption.title = value
    setLangOptions([...langOptions])
    selectedItem.lang_data = plainListToGql(langOptions)

  }
  const loadCountryOptions=(inputValue, callback)=>{
    CountryService.getCountries({page: 1, pageSize: 1000, keyword:inputValue},response=>{
      console.log("response on load countryoption.....>>>>",response)
      if(response.status === 1){
          const operators = response.data.results
          const tempOptions = []
          operators.forEach(element => {
              const obj = {
                label:element.country_name,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          callback(tempOptions)
        }
    })
  };
  const getSelectedOption = (value)=>{
    const found = countryOptions.find((item)=>{
       return(
         item.label === value
       )
     })
     return found;
 }
 const handleSubmit = (e)=>{
  e.stopPropagation();
  e.preventDefault();
  onSubmit();
}
  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
          {selectedItem._id?"Edit State":"Add New State"}
      </ModalHeader>
      <Form onSubmit={handleSubmit}>
      <ModalBody>
       <Label className="mt-4">
            Name
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'state_name')}} value={selectedItem.state_name}/>
        <Label className="mt-4">
           Country 
        </Label>
        <AsyncSelect
          required
          components={{ Input: CustomSelectInput }}
          className="react-select"
          name="form-field-name"
          loadOptions={loadCountryOptions}
          defaultOptions
          value={getSelectedOption(selectedItem.country_code)}
          onChange={(e)=>{onChangeProperty(e.label,'country_code')}}
          // onInputChange={this.handleInputChange}
        />
        {/* <Input onChange={(e)=>{onChangeProperty(e.target.value,'country_code')}} value={selectedItem.country_code}/> */}
        <LanguageSelector 
            langOptions={langOptions} 
            onChangeName={(e)=>{onChangeName(e)}}
            selectedLangIndex = {selectedLangIndex}
            onChangeTab = {(index)=>{setSelectedLangIndex(index)}}
        />
      </ModalBody>
      
      <ModalFooter>
        <Button color="secondary" outline onClick={closeModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button color="primary" type="submit">
          <IntlMessages id="pages.submit" />
        </Button>{" "}
      </ModalFooter>
      </Form>
    </Modal>
  );
};

export default AddNewModal;
