import React from "react";
import {
  CustomInput,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Row, 
  Form
  
} from "reactstrap";
import "react-datepicker/dist/react-datepicker.css";
import IntlMessages from "../../helpers/IntlMessages";

const AddNewModal = ({ modalOpen,selectedItem, toggleModal,onSubmit,closeModal,onChangeProperty }) => {
  const {start_date,end_date} = selectedItem
  const handleSubmit = (e)=>{
    e.stopPropagation();
    e.preventDefault();
    onSubmit();
  }
  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
          {selectedItem._id?"Edit Operator":"Add New Operator"}
      </ModalHeader>
      <Form onSubmit={handleSubmit}>
      <ModalBody>
        <Label>
            Partner Identifier
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'operator_partner_identifier')}} value={selectedItem.operator_partner_identifier}/>
        <Label className="mt-4">
            API endpoint
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'api_endpoint')}} value={selectedItem.api_endpoint}/>
      </ModalBody>
      <ModalFooter>
        <Button color="secondary" outline onClick={closeModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button color="primary">
          <IntlMessages id="pages.submit" type="submit"/>
        </Button>{" "}
      </ModalFooter>
      </Form>
    </Modal>
  );
};

export default AddNewModal;
