import React,{useState,useEffect} from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Form
} from "reactstrap";
import "react-datepicker/dist/react-datepicker.css";
import CustomSelectInput from "../../components/common/CustomSelectInput";
import AsyncSelect from 'react-select/async'
import IntlMessages from "../../helpers/IntlMessages";
import OperatorService from '../../service/graphql/Operator'
import StateService from '../../service/graphql/State'
import CountryService from '../../service/graphql/Country'
import CityService from '../../service/graphql/City'

const AddNewModal = ({ modalOpen,selectedItem, toggleModal,onSubmit,closeModal,onChangeProperty }) => {
  const [operatorOptions, setOperatorOptions] = useState([])
  const [stateOptions, setStateOptions] = useState([])
  const [cityOptions, setCityOptions] = useState([])
  const [countryOptions, setCountryOptions] = useState([])
  const [isSubmitting, setIsSubmitting] = useState(false)
  console.log('selected hubItem...',selectedItem)
  const  getOperators = (data)=>{
    OperatorService.getOperators(data, response=>{
        if(response.status === 1){
          const operators = response.data.operators
          const tempOptions = []
          operators.forEach(element => {
              const obj = {
                label:element.operator_partner_identifier,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          setOperatorOptions(tempOptions)
        }
    })
  }
  const getCountries = ()=>{
    CountryService.getCountries({page: 1, pageSize: 1000, keyword:""}, response=>{
        if(response.status === 1){
          const countries = response.data.results  
          const tempOptions = []
          countries.forEach(element => {
              const obj = {
                label:element.country_title,
                value:element.country_code,
                id:element.country_code,
              }
              tempOptions.push(obj)
          });          
          console.log('countryOptions', countries, tempOptions);          
          setCountryOptions(tempOptions)
        }
    })
  }
  const getCities = ()=>{
    CityService.getCities(0, 10, '', response=>{
        if(response.status === 1){
          const cites = response.data.results
          const tempOptions = []
          cites.forEach(element => {
              const obj = {
                label:element.city_name,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          setCityOptions(tempOptions)
        }
    })
  }
  const getStates = ()=>{
    StateService.getStates(1,10,"",response => {
        if(response.status === 1){
          const states = response.data.results
          const tempOptions = []
          states.forEach(element => {
              const obj = {
                label:element.state_name,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          setStateOptions(tempOptions)
        }
    })
  }
  const loadStateOptions = (inputValue, callback)=>{    
    StateService.getStates(1, 1000, "",response=>{
      if(response.status === 1){
          const states = response.data.results
          const tempOptions = []
          states.forEach(element => {
              const obj = {
                label:element.state_name,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          callback(tempOptions)
        }
    })
  };
  const loadCountryOptions=(inputValue, callback)=>{
    CountryService.getCountries({page: 1, pageSize: 1000, keyword:""}, response=>{
      if(response.status === 1){
          const countries = response.data.results          
          const tempOptions = []
          countries.forEach(element => {
              const obj = {
                label:element.country_title,
                value:element.country_code,
                id:element.country_code,
              }
              tempOptions.push(obj)
          });
          callback(tempOptions)
        }
    })
  };
  const loadCityOptions=(inputValue, callback)=>{
    CityService.getCities(1, 1000, "", response=>{
      if(response.status === 1){
          const cities = response.data.results
          const tempOptions = []
          cities.forEach(element => {
              const obj = {
                label:element.city_name,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          callback(tempOptions)
        }
    })
  };
  useEffect(()=>{
    getOperators({page:1 , pageSize: 100})
    getCities()
    getStates()
    getCountries()
  },[selectedItem])
  const loadOptions = (inputValue, callback) => {
    console.log('operators options--->>>',inputValue)
    OperatorService.getOperators({page:1 , pageSize: 100},response=>{
      if(response.status === 1){
          const operators = response.data.operators
          const tempOptions = []
          operators.forEach(element => {
              const obj = {
                label:element.operator_partner_identifier,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          callback(tempOptions)
        }
    })
  };
  const getSelectedOption =(value,type)=>{
    let options = []

    if(type==="city"){
      options = cityOptions
    }
    if(type==="operator"){
      options = operatorOptions
    }
    if(type==="state"){
      options = stateOptions
    }
    if(type==="country"){
      options = countryOptions
      console.log('countryOptions--', countryOptions);
    }
    
    
    const found = options.find((item)=>{
       return(
         item.value === value
       )
     })
     return found;
 }
 const handleSubmit = (e)=>{
  e.stopPropagation();
  e.preventDefault();

  setIsSubmitting(true)
  if(!selectedItem.country_code || !selectedItem.state_id  || !selectedItem.city_id || !selectedItem.hub_operator_id){
    return
  }
  console.log('handleSubmit');
  onSubmit();
}
  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
          {selectedItem._id?"Edit Hub":"Add New Hub"}
      </ModalHeader>
      <Form onSubmit={handleSubmit}>
      <ModalBody>
        <Label>
            Identifier 
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'hub_identifier')}} value={selectedItem.hub_identifier}/>
        <Label className="mt-4">
            Longitude
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value, 'hub_lon')}} value={selectedItem.hub_lon}/>
        <Label className="mt-4">
            Latitude
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value, 'hub_lat')}} value={selectedItem.hub_lat}/>
        <Label className="mt-4">
          Radius
        </Label>
        <Input onChange={(e)=>{onChangeProperty(e.target.value,'hub_radius')}} value={selectedItem.hub_radius}/>
        <Label className="mt-4">
            Address
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'hub_address')}} value={selectedItem.hub_address}/>
        <Label className="mt-4">
            Operator 
        </Label>
        <AsyncSelect
          required = {true}
          components={{ Input: CustomSelectInput }}
          className="react-select"
          name="form-field-name"
          loadOptions={loadOptions}
          defaultOptions
          value={getSelectedOption(selectedItem.hub_operator_id,'operator')}
          onChange={(e)=>{onChangeProperty(e.value,'hub_operator_id')}}
        />
         {
          isSubmitting && !selectedItem.hub_operator_id &&
          <div className="invalid-feedback d-block">
                {"Operator required!"}
          </div>
         }
        {/* <Label className="mt-4">
            Country Code
        </Label>
        <Input required onChange={(e)=>{onChangeProperty(e.target.value,'country_code')}} value={selectedItem.country_code}/> */}
        <Label className="mt-4">
            Country Code
        </Label>
        <AsyncSelect
          required
          components={{ Input: CustomSelectInput }}
          className="react-select"
          name="form-field-name"
          loadOptions={loadCountryOptions}
          defaultOptions
          value={getSelectedOption(selectedItem.country_code,'country')}
          onChange={(e)=>{onChangeProperty(e.value,'country_code')}}
          // onInputChange={this.handleInputChange}
        />
        {
          isSubmitting && !selectedItem.country_code &&
          <div className="invalid-feedback d-block">
                {"Country required!"}
          </div>
        }
        <Label className="mt-4">
            State
        </Label>
        <AsyncSelect
          required
          components={{ Input: CustomSelectInput }}
          className="react-select"
          name="form-field-name"
          loadOptions={loadStateOptions}
          defaultOptions
          value={getSelectedOption(selectedItem.state_id,'state')}
          onChange={(e)=>{onChangeProperty(e.value,'state_id')}}
        />
        {
          isSubmitting && !selectedItem.state_id &&
          <div className="invalid-feedback d-block">
                {"State required!"}
          </div>
        }
        <Label className="mt-4">
            City 
        </Label>
        <AsyncSelect
          required
          components={{ Input: CustomSelectInput }}
          className="react-select"
          name="form-field-name"
          loadOptions={loadCityOptions}
          defaultOptions
          value={getSelectedOption(selectedItem.city_id,'city')}
          onChange={(e)=>{onChangeProperty(e.value,'city_id')}}
        />
        {
          isSubmitting && !selectedItem.city_id &&
          <div className="invalid-feedback d-block">
                {"City required!"}
          </div>
        }
      </ModalBody>
      
      <ModalFooter>
        <Button color="secondary" outline onClick={closeModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button color="primary" type="submit">
          <IntlMessages id="pages.submit" />
        </Button>{" "}
      </ModalFooter>
      </Form>
    </Modal>
  );
};

export default AddNewModal;
