import React, { Component, Fragment } from "react";
import IntlMessages from "../../../helpers/IntlMessages";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Switch from "rc-switch";
import Select from 'react-select';
import Config from '../../../service/api/config'
import 'react-tabs/style/react-tabs.css';
import {
  Row,
  Card,
  CardBody,
  Input,
  CardTitle,
  CustomInput,
  Button,
  Label
} from "reactstrap";
import "react-tagsinput/react-tagsinput.css";
import "react-datepicker/dist/react-datepicker.css";
import "rc-switch/assets/index.css";
import "rc-slider/assets/index.css";
import "react-rater/lib/react-rater.css";
import AsyncSelect from 'react-select/async'
import CategoryService from '../../../service/graphql/Category'
import { NotificationManager } from "../../../components/common/react-notifications";
import LanguageSelector from '../../../components/LanguageSelector'
import {plainListToGql,gqlToLanguageOptions,getDefaultLanguageOptions} from '../../../libs/converter'


export default class CategoryDetail extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedOption: "",
      selectedItem:this.props.location.state.selectedItem?this.props.location.state.selectedItem:{},
      categoryName:this.props.location.state.categoryName?this.props.location.state.categoryName:"",
      selectedItemActive:this.props.location.state.selectedItem?this.props.location.state.selectedItem.prodcat_active:false,
      selectedItemFeatured:this.props.location.state.selectedItem?this.props.location.state.selectedItem.prodcat_featured:false,
      categoryOptions:this.props.location.state.listCategories?this.props.location.state.listCategories:[],
      prodcatId:'',
      langs:[],
      langOptions:[],
      selectedLangIndex:0,
      listCategories:[],
      inputValue:'',
      editState:false,
      switchCheckedPrimary: false,
      path: [{ name: 'Home', id: '', list: {} }, { name: 'Categories', id: '', list: {} }],
    };
  }
    componentDidMount(){
      if(this.state.selectedItem._id){
        this._getCategory() 
      }
      else{
        this._getLangOptions()  
      }
      console.log('props...',this.props)
      this.setState({editState:false})
    }

    _getLangOptions(){
      getDefaultLanguageOptions((options)=>{
        this.setState({langOptions:options})
      })
    }
    
    _getCategory(){
      const {selectedItem}= this.state
      const params = {
        categoryId: selectedItem._id,
      };
      CategoryService.getCategory(params,response=>{
        console.log('getCategory res...',response)
        if (response.status === 1) {
          const categories = response.data;
          if(categories.langs&&categories.langs.length>0){
            this.setState({langOptions:gqlToLanguageOptions(categories.langs,"prodcat_label")})
          }
        } else {
        }
    })
    }
    onChangeProperty(value,type){
      console.log('value,type...',value,type)
      const {selectedItem} = this.state
      if(type==='prodcat_active'){
        // selectedItem[type]=!selectedItem[type]
        selectedItem[type]=value
        this.setState({selectedItemActive:value})
      }else if(type==='prodcat_featured'){
        // selectedItem[type]=!selectedItem[type]
        selectedItem[type]=value
        this.setState({selectedItemFeatured:value})
      }
      else if(type==='prodcat_image'){
        this.setState({editState:true})
        let reader = new FileReader();
        reader.readAsDataURL(value);
        reader.onloadend = () => {
        selectedItem[type] = reader.result
        console.log("value on prodcat_image--->>>", reader.result)
        this.setState({selectedItem})          
      };
      }
      else{
        selectedItem[type] = value
      }
      this.setState({selectedItem}) 
      console.log("selectedItem--->>>",selectedItem)
    }
    onSubmit(e){
      const {selectedItem,langOptions} = this.state
      let categoryItems=[]
      let pathTmp=[]
      let pathName=''
      let pathId=''
      if(selectedItem.prodcat_identifier === undefined ||selectedItem.prodcat_identifier ===''){
        NotificationManager.error(
          "Error message",
          "Please confirm valid data!",
          3000,
          () => {
          },
          null,
          "error"
        );
        return;
      }
      // let langData = '['
      // for (let i = 0; i < langDataTmp.length; i++) {
      //   langData += `{lang_code: "${langDataTmp[i].lang_code}",title: "${langDataTmp[i].title}",description: "${langDataTmp[i].description}"},`
      // }
      // langData += ']'
      const langData= plainListToGql(langOptions)
      const params={
        prodcatId: selectedItem._id?selectedItem._id:'', 
        prodcatParent: selectedItem.prodcat_parent?selectedItem.prodcat_parent:'', 
        prodcatActive: selectedItem.prodcat_active?true:false, 
        prodcatFeatured: selectedItem.prodcat_featured?true:false, 
        prodcatImage: selectedItem.prodcat_image?selectedItem.prodcat_image:'', 
        prodcatIdentifier:selectedItem.prodcat_identifier?selectedItem.prodcat_identifier:'',
        prodcatDisplayOrder: selectedItem.prodcat_display_order?selectedItem.prodcat_display_order:0,                        
        langData:langData
      }
      const parentCategory=this.state.categoryOptions.find(item=>item.value===selectedItem.prodcat_parent)  
      if(parentCategory){
        categoryItems=parentCategory.label.split(' >> ')
      }
      if(categoryItems&&categoryItems.length>0){
        categoryItems.map((item)=>{
          pathName=pathName?pathName.concat(' >> ').concat(item) :item
          pathId=(this.state.categoryOptions.find(item=>item.label===pathName)).value
          const obj={name:item, id:pathId}
          pathTmp.push(obj)
        })
      }
      CategoryService.postCategory(params,(error,res)=>{
        console.log('postCategory...',res)
        this.props.history.push({
          pathname: `/app/categories/list`,
          state: { item:this.state.selectedItem,path:pathTmp }
        })
      })
    }
    onclose(e){
      // this.props.history.push(`/app/categories/list`)
      this.props.history.push({
        pathname: `/app/categories/list`,
        state: { detail: 'some_value' }
      })
    }
  
  _getCategories(){
    var responseCategories = [];
    var id=''
    const params = {
      langCode: "en",
    };
    CategoryService.getCategories(params,response=>{
      if (response.status === 1) {
        const categories = response.data;
        for (id of Object.keys(categories)) {
          responseCategories.push(categories[id]);
        }
      } else {
      }
      this.setState({categories:responseCategories})
    })
  }
  onChangeName(value){
    const {selectedLangIndex} = this.state
    const selectedLangOption = this.state.langOptions[selectedLangIndex];
    selectedLangOption.title = value
    this.setState({langOptions:[...this.state.langOptions]})
  }
  
  loadOptions = (inputValue, callback) => {
    var listCategories = [];
    const params = {
      parentId:"",
      keyword:"",
      langCode: "en"
    };
    CategoryService.getListCategories(params,response=>{
      if (response.status === 1) {
        const optionsTmp = []
        listCategories= response.data
        listCategories.forEach(element => {
          const obj = {
            label:element.name,
            value:element.prodcat_id,
            id:element.prodcat_id,
          }
          optionsTmp.push(obj)
        });
        console.log('categoryOptions...',optionsTmp)
        this.setState({categoryOptions:optionsTmp})
        setTimeout(() => {
          callback(this.filterCategories(inputValue));
        }, 1000);
      } else {
      }
    })
  };
  getSelectedOption(value){
    const found = this.state.categoryOptions.find((item)=>{
      return(
        item.value === value
      )
    })
    return found;
  }
  filterCategories = (inputValue: string) => {
    const {categoryOptions}= this.state
    return categoryOptions.filter((i) =>
      i.label.toLowerCase().includes(inputValue.toLowerCase())
    );
  };
  handleInputChange = (newValue: string) => {
    console.log('newValue...',newValue)
    if(!newValue){
      this.setState({ inputValue:'' });
      return inputValue;
    }
    const inputValue = newValue.replace(/\W/g, "");
    this.setState({ inputValue });
    return inputValue;
  };
    
  render() {
    const {selectedItem,editState,selectedItemActive,selectedItemFeatured} = this.state
      return (
        <Fragment>
          <Row>
            <Colxx xxs="12">
              <Breadcrumb heading={selectedItem._id?"Edit Category":"Add New Category"} match={this.props.match} />
              <Separator className="mb-5" />
            </Colxx>
          </Row>
          <Row className="mb-4">
        <Colxx xxs="12" md="12">
          <Card>
            <CardBody>
              <CardTitle>
                Input Details
              </CardTitle>
              <div className="d-flex">
                <div className="flex-1">
                  {/* <Form onSubmit={this.onSubmit}> */}
                  <Label>
                    Identifier
                  </Label>
                  <Input required onChange={(e)=>{this.onChangeProperty(e.target.value,'prodcat_identifier')}} value={selectedItem.prodcat_identifier}/>
                  <Label className="mt-4">
                    Categories
                  </Label>
                  <AsyncSelect
                    cacheOptions
                    className="react-select"
                    loadOptions={this.loadOptions}
                    defaultOptions
                    value={this.getSelectedOption(selectedItem.prodcat_parent)}
                    isClearable={true}
                    onInputChange={this.handleInputChange}
                    onChange={(e)=>{this.onChangeProperty((e&&e.value)?e.value:'','prodcat_parent')}}
                  />
                  <Label className="mt-4">
                    Category Order
                  </Label>
                  <Input  required  onChange={(e)=>{this.onChangeProperty(e.target.value,'prodcat_display_order')}} value={selectedItem.prodcat_display_order}/>
                  <LanguageSelector 
                    langOptions={this.state.langOptions} 
                    onChangeName={this.onChangeName.bind(this)}
                    selectedLangIndex = {this.state.selectedLangIndex}
                    onChangeTab = {(index)=>{this.setState({selectedLangIndex:index})}}
                  />
                  <Button color="secondary" className="mt-4" outline
                  onClick={()=>{
                    this.onclose()}}>
                    <IntlMessages id="forms.cancel" />
                  </Button>
                  <Button style={{marginLeft:'15px'}} color="primary" className="mt-4" type="submit" onClick={()=>{
                    this.onSubmit()}}>
                    <IntlMessages id="forms.submit" />
                  </Button>
                  
                </div>
                <div className="flex-1">
                  <Label >
                      Image
                  </Label>
                  <CustomInput
                    type="file"
                    id="exampleCustomFileBrowser1"
                    name="customFile"
                    onChange={(e)=>{this.onChangeProperty(e.target.files[0],'prodcat_image')}}
                  />
                  {
                    (selectedItem._id && selectedItem.prodcat_image && !editState) &&
                      <img className="shop-image" src={Config.BACKEND_API_URL + selectedItem.prodcat_image}/> 
                  }
                  {
                    editState && <img className="shop-image" src={selectedItem.prodcat_image}/>
                  }
                  <div>
                  <Label className="mt-4">
                    Active
                  </Label>
                  <Switch
                    className="custom-switch custom-switch-primary custom-switch-small"
                    checked={selectedItemActive}
                    onChange={value=>{this.onChangeProperty(value, 'prodcat_active')}}
                  />
                  </div>
                  <Label className="mt-4">
                    Featured
                  </Label>
                  {/* <CustomInput
                    className="itemCheck mb-0"
                    type="checkbox"
                    id={`check_${selectedItem.prodcat_identifier}`}
                    checked={selectedItem.prodcat_featured?true:false}
                    // onChange={(e)=>{onChangeProperty(e.target.value, 'prodcat_active')}}
                    onChange={event => this.onChangeProperty(event, 'prodcat_featured')}
                    label=""
                  /> */}
                
              <Switch
                className="custom-switch custom-switch-primary custom-switch-small"
                checked={selectedItemFeatured}
                onChange={value=>{this.onChangeProperty(value, 'prodcat_featured')}}
              />
                  {/* <Select
                    defaultValue={categoryOptions[0]}
                    options={categoryOptions}
                    isClearable={true}
                    isSearchable={true}
                    // formatGroupLabel={formatGroupLabel}
                  /> */}
              
            {/* </Form> */}
            </div>
            </div>
          </CardBody>
        </Card>
      </Colxx>
      </Row>
      </Fragment>
    )
  }
}
